<template>
  <div>
    <component :is="toggle 
                  ? 'v-btn-toggle' 
                  : 'div'" 
               v-bind="toggle 
                  ? { vModel: selectedButton, group: true, variant: 'outlined', divided: true} 
                  : {}"
              >
      
        <v-btn v-if="!hideRunning" variant="elevated" value="RUNNING"
          color="rgba(91, 192, 222, 1)"
          :text="item?.status[0].toString() || '0'"
          :class="getButtonClass('running')"
          size="small"
          :to="noLinks ? null : setRoute('RUNNING')"
          @click="noLinks ? null : replaceRoute('RUNNING')"
        ></v-btn>
        <span v-if="!(toggle || hideRunning)">/ </span>
        <v-btn variant="elevated" value="SUCCESS"
          color="rgba(92, 184, 92, 1)"
          :text="item?.status[1].toString() || '0'"
          :class="getButtonClass('success')"
          size="small"
          :to="noLinks ? null : setRoute('SUCCESS')"
          @click="noLinks ? null : replaceRoute('SUCCESS')"
        ></v-btn>
        <span v-if="!toggle">/ </span>
        <v-btn variant="elevated" value="FAIL"
          color="rgba(217, 83, 79, 1)"
          :text="item?.status[2].toString() || '0'"
          :class="getButtonClass('fail')"
          size="small"
          :to="noLinks ? null : setRoute('FAIL')"
          @click="noLinks ? null : replaceRoute('FAIL')"
        ></v-btn>
        <span v-if="!toggle">/ </span>
        <v-btn variant="elevated" value="FAILED3TIMES"
          color="rgba(139, 0, 0, 1)"
          :text="item?.status[3].toString() || '0'"
          :class="getButtonClass('fail3')"
          size="small"
          :to="noLinks ? null : setRoute('FAILED3TIMES')"
          @click="noLinks ? null : replaceRoute('FAILED3TIMES')"
        ></v-btn>
        <span v-if="!(toggle || hideTotal)">/ </span>
        <v-btn v-if="!hideTotal" variant="elevated" value="ALL" 
          color="rgb(128,128,128)"
          :text="item?.status[4].toString() || '0'"
          :class="getButtonClass('total')"
          size="small"
          :to="noLinks ? null : setRoute('ALL')"
          @click="noLinks ? null : replaceRoute('ALL')"
        ></v-btn>
    </component>
  </div>
</template>

<script>
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    item: { type: Object, required: true, default: () => ({ status: [0,0,0,0,0], jobId: null}) },
    toggle: {type: Boolean, default: () => {false}},
    hideTotal: Boolean,
    hideRunning: Boolean,
    noLinks: Boolean
  },
  data() {
    return {
      selectedButton: 'empty'
    }
  },
  methods: {
    getButtonClass(button) {
      const inactiveClassName = `inactive-button-${button}`
      // const isSelected = this.selectedButton == button;
      return {
        'text-uppercase' : true,
        'chip-button': true,
        // 'active-button-font': isSelected,
        [inactiveClassName]: true, // Apply 'selected' class if button is active
      };
    },
    setRoute(path) {
      return { name: 'JobDetails', params: { id: this.item?.jobId, status: path} }
    },
    replaceRoute(path) {
      if (this.toggle) {
        this.$router.replace({
          name: 'JobDetails', 
          params: {
            id: this.item?.jobId,
            status: path
          }
        })
      }
    }
  }
})
</script>

<style scoped>
.v-btn-group--density-default.v-btn-group {
  height: unset !important; /* Override the default height */
}

.chip-button {
  font-size: 1em;
  font-weight: bolder !important;
  height: 40px !important;
  width: 60px;
  min-width: 32px; /* Prevents button from expanding */
  padding: 0;      /* Removes default padding for a snug fit */
}

.inactive-button-running {
  border: 2px solid #5bc0de;
  color: #5bc0de;
}

.inactive-button-success {
  border: 2px solid #5cb85c;
  color: #5cb85c;
}

.inactive-button-fail {
  border: 2px solid #d9534f;
  color: #d9534f;
}

.inactive-button-fail3 {
  border: 2px solid #8b0000;
  color: #8b0000;
  
}
.inactive-button-total {
  border: 2px solid grey;
  color: grey;
}

.active-button-font {
  color: white !important;
  background: red;
}
</style>