<template>
  <slot></slot>
  <v-snackbar v-model="snackbar" zIndex="100" location="top" timeout="3000">
    <div class="text-h6"><strong>WebSocket: </strong> {{ snackMessage }}</div>
    <template v-slot:actions>
      <v-btn
        color="primary"
        variant="text"
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { ref, provide, onBeforeUnmount } from 'vue';
import StorageHelper from './StorageHelper';
import eventBus from "./eventBus";

export default {
  setup() {
    const snackbar = ref(false);
    const snackMessage = ref();
    const job = ref();
    const agent = ref();
    const test = ref();
    // Provide the messages array to child components
    provide('job-update', job);
    provide('modified-agent', agent);
    provide('test-update', test);

    const wssHost = `${window.config.WEBSOCKET_PROTOCOL}://${window.config.HOST}:${window.config.WEBSOCKET_PORT}/events`;
    let ws = null;
    let connectionInterval = null; // Store the interval ID

    const initializeWebSocket = () => {
      try {
        ws = new WebSocket(wssHost);
        console.log("Attempting to subscribe to: " + wssHost);

        ws.onopen = () => {
          snackMessage.value = "Connection established";
          snackbar.value = true;
          clearInterval(connectionInterval); // Stop retrying once connected
        };

        ws.onmessage = (event) => {
          try {
            let parsedData = JSON.parse(event.data); // Assuming the message is JSON
            switch (parsedData.id) {
              case "modified-job":
              case "created-job":
                job.value = parsedData.content;
                if (StorageHelper.hasRawJobsSaved()) {
                  StorageHelper.updateRawJob(job.value);
                }
                return;
              case "modified-agents-count":
                eventBus.$emit("modified-agents-count", parsedData.content);
                return;
              case "modified-failing-agents":
                eventBus.$emit("modified-failing-agents", parsedData.content);
                return;
              case "modified-test":
                test.value = parsedData.content;
                return;
              case "deleted-agent":
              case "modified-agent":
                parsedData.content.action = parsedData.id // save action
                agent.value = parsedData.content;
                return;
            }
          } catch (t) {
            console.error('Error parsing message:', t);
            snackMessage.value = t;
            snackbar.value = true;
          }
        };

        ws.onerror = (t) => {
          if (snackMessage.value == null) {
            snackMessage.value = "Automatic updates are not available";
            snackbar.value = true;
          }
          ws.close(); // Close to trigger a retry
        };
      } catch (error) {
        console.error("Unable to access WebSocket host: " + error);
      }
    };

    initializeWebSocket();

    // Retry connection every 5 seconds until successful
    connectionInterval = setInterval(initializeWebSocket, 5000);

    // Cleanup on component unmount
    onBeforeUnmount(() => {
      if (ws) {
        ws.close(); // Close WebSocket if still open
        console.log("websocket closed")
      }
      clearInterval(connectionInterval); // Stop retry interval
    });

    return { snackMessage, snackbar };
  },
  watch: {
    snackbar(isShown) {
    }
  },
  data() {
    return {
      
    }
  }
}
</script>
