<template>
  <v-card align="center">
    <div style="width: 100%">
      <v-card-title>
        <v-row dense class="pa-0 ma-0">
          <v-col cols="3" alogn="start">
            <v-text-field
              v-model="search"
              density="compact"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              variant="outlined"
              hide-details
              single-line
              clearable
            ></v-text-field>
          </v-col>
          <v-spacer></v-spacer>
          <v-col cols="3" align="end">
            <v-dialog max-width="700">
              <template v-slot:activator="{ props: activatorProps }">
                <v-btn value="info"
                  v-bind="activatorProps"
                  rounded
                  icon="mdi-information-box"
                  size="default"
                  color="primary"
                  max-height="32px"
                  max-width="32px"
                ></v-btn>
              </template>

              <template v-slot:default="{ isActive }">
                <v-card title="Agents info">
                  <v-card-text>
                    <v-textarea
                      readonly
                      density="compact"
                      v-model="agentsInfo"
                      variant="outlined"
                      style="font-family: 'Trebuchet MS', monospace"
                      rows=20
                    ></v-textarea>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                      class="btn-confirm" 
                      width="120" 
                      elevation="5" 
                      variant="outlined"
                      text="CLOSE"
                      @click="isActive.value = false"
                    ></v-btn>
                  </v-card-actions>
                </v-card>
              </template>
            </v-dialog>
          </v-col>
        </v-row>
      </v-card-title>

      <v-data-table
        :search="search"
        density="compact"
        :headers="headers"
        :items="agents"
        class="agentsTable elevation-1"
        hover
        :loading="loading"
        :items-per-page="itemsPerPage"
        :items-per-page-options="[15, 20, 25, 50]"
      >

        <template v-slot:item.name="{ item }">
          <span v-html="agentName(item.name)"></span>
        </template>

        <template v-slot:item.state="{ item }">
          <div align="center">
            <v-chip
              variant="elevated"
              class="text-uppercase font-weight-bold text-body-2 text-mono"
              :color="getStatusColor(item.state)"
              :text="item.state"
              size="small"
              label
            ></v-chip>
          </div> 
        </template>

        <template v-slot:item.actions="{ item }">
          <v-btn
            tile
            icon="mdi-delete"
            height="26px"
            width="26px"
            rounded
            color="#D30000"
            class="mr-1"
            @click="deleteAgentPrompt(item)"
          />
        </template>
      </v-data-table>
    </div>
  </v-card>

  <!-- Delete Agent -->
  <prompt-dialog
    ref="agentDeleteDialog"
    prepend-icon="mdi-alert"
    icon-color="orange"
    title="Delete Agent"
    @promptDialog:onConfirm="confirmDeleteAgent"
    @promptDialog:onCancel="cancelDeleteAgent"
  >
    <dialog-prompt-body :text="deleteAgentDialogBody" />
  </prompt-dialog>
</template>

<script setup>
import { getStatusColor } from '@/utils/utils';
</script>

<script>
  import { inject } from 'vue';
  import { parseAgentEntry } from '@/utils/utils';
  import PromptDialog from '../modals/PromptDialog.vue';
  import DialogPromptBody from '../modals/DialogPromptBody.vue';

  export default {
    components: {
      PromptDialog,
      DialogPromptBody,
    },
    data () {
      return {
        loading: true,
        agentUpdate: null,
        itemsPerPage: 20,
        deleteAgent: null,
        agentsInfo: null,
        agents: [],
        search: null,
        headers: [
          { key: "name", title: "Name", width: "12%"},
          { key: "capabilities", title: "Type", width: "12%"},
          { key: "state", title: "State", width: "7%", maxWidth: "3%"},
          { key: "host", title: "Host", width: "12%", maxWidth: "3%"},
          { key: "pid", title: "PID", width: "5%"},
          { key: "retries", title: "Retries", width: "1%"},
          { key: "jobId", title: "Job", width: "7%", maxWidth: "3%"},
          { key: "build", title: "Build", width: "7%", maxWidth: "3%"},
          { key: "suite", title: "Suite", width: "7%", maxWidth: "3%"},
          { key: "tests", title: "Current Tests", width: "7%", maxWidth: "3%"},
          { key: "lastSeen", title: "Last Seen", width: "10%"},
          { key: "group", title: "Group", width: "7%", maxWidth: "3%"},
          { key: "actions", title: "Actions", width: "7%", maxWidth: "3%"},
        ],
      }
    },
    beforeMount() {
      this.agentUpdate = inject('modified-agent');
      this.initTable();
      this.initAgentsInfo();
    },
    computed: {
      deleteAgentDialogBody() {
        if (this.deleteAgent == null) return "";

        return `Would you like to delete agent\n<strong>${this.deleteAgent.name}\n[pid: ${this.deleteAgent.pid}]</strong> ?`;
      }
    },
    watch: {
      agentUpdate(agent) {
        if (agent && !this.loading) {
          const udpdatedAgent = parseAgentEntry(agent);

          const existingAgentIndex = this.agents.findIndex(
            (item) => item.id === agent.id
          );
          if (existingAgentIndex !== -1) {
            if (udpdatedAgent.action == 'deleted-agent') {
              this.agents.splice(existingAgentIndex, 1)
            } else {
              this.agents[existingAgentIndex] = udpdatedAgent;  // Update only the specific item without affecting the rest
            }
          } else {
            this.agents.unshift(udpdatedAgent);
          }
        }
      }
    },
    methods: {
      initTable() {
        this.$axios
          .get("/api/newman/agent", {params: {all: true}})
          .then((response) => {
            this.agents = response.data.values.map(parseAgentEntry); // Update the component's data
            this.loading = false;
          })
          .catch((error) => {
            console.error("Error fetching data:", error);
            this.loading = false;
          });
      },
      initAgentsInfo() {
        this.$axios.get("/api/spotinst/elasticgroup")
          .then((response) => {
            this.agentsInfo = JSON.stringify(response.data, null, 4); // Update the component's data
          })
          .catch((error) => {
            console.error("Error fetching data:", error);
          });
      },
      deleteAgentPrompt(item) {
        this.deleteAgent = item;
        this.$refs.agentDeleteDialog.openDialog();
      },
      agentName(name) {
        let nameArr = name.split(/-(.+)/)
        return `<strong>${nameArr[0]}</strong>-${nameArr[1]}`;
      },
      confirmDeleteAgent() {
        this.dialogDisabled = true;
        if (Array.isArray(this.deleteItem)) {
          this.selectedDelete();
          //this.deleteItem = null;
        } else {
          this.$axios
            .delete(`/api/newman/agent/${this.deleteAgent.id}`)
            .then((response) => {
              const agentIndex = this.agents.findIndex(
                (item) => item.id === this.deleteAgent.id  // find item among others
              );
              if (agentIndex !== -1) {
                this.agents.splice(agentIndex, 1);   // delete it by the index
              } else {
                console.info("Agent not found");
              }
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
            });
        }
      },
      cancelDeleteAgent() {
        this.deleteAgent = null;
      },
    }
  }
</script>

<style>
.agentsTable > .v-table__wrapper > table > thead > tr > th > .v-data-table-header__content{
  font-weight: bold;
  text-align: center;
  display: block;
}

.agentsTable > .v-table__wrapper > table > tbody > .v-data-table__tr {
  font-size: 1rem;
  font-weight: 500;
}

.agentsTable > .v-table__wrapper > table > tbody > tr > td {
  /* white-space: nowrap; */
  overflow: hidden;
  text-overflow: ellipsis;
}

.agentsTable {
  width: 100% !important;
}

.agentsTable tbody tr:nth-child(even) {
  background: hsla(230, 86%, 73%, 0.075);
}
</style>