<template>
  <v-card align="center">
    <v-container fluid class="m-16 mt-5" style="width: 40%;">
      <!-- Suites -->
      <v-row>
        <v-col cols="12" class="pb-0">
          <v-autocomplete
            variant="outlined"
            density="compact"
            color="blue"
            v-model="suitesValues"
            :items="suites"
            item-title="name"
            item-value="id"
            chips
            multiple
            clearable
            :rules="[required]"
            required
            label="Select suites"
            ref="suite"
            :menu-props="{ maxHeight: '600' }"
          >
            <template v-slot:chip="{ item }">
              <v-chip
                color="blue"
                closable
                size="large"
                @mousedown.stop
                @click:close="deselectSuite('suites', item.value)"
              >
                {{ item.title }}
              </v-chip>
            </template>
          </v-autocomplete>
        </v-col>
      </v-row>
      <!-- JDK -->
      <v-row>
        <v-col cols="12" class="pb-0">
          <v-autocomplete
            variant="outlined"
            density="compact"
            color="blue"
            v-model="configValue"
            :items="configs"
            item-title="name"
            item-value="id"
            label="Select Job Configuration"
            return-object
          >
        </v-autocomplete>
        </v-col>
      </v-row>
      <!-- Build -->
      <v-radio-group v-model="buildType" column ref="build">
        <v-radio density="compact" label="Select Build" value="buildBySelect"></v-radio>
        <v-row>
          <v-col cols="12" class="pb-0">
            <v-autocomplete
              variant="outlined"
              density="compact"
              color="blue"
              v-model="buildValue"
              :items="formattedBuilds"
              :rules="buildType === 'buildBySelect' ? [required] : []"
              :disabled="buildType != 'buildBySelect'"
              :menu-props="{ maxHeight: '600', contentClass: 'autocomplete-overlay' }"
              item-title="label"
              item-value="id"
              required
              ref="buildBySelect"
            >
            </v-autocomplete>
          </v-col>
        </v-row>
        <!-- Build by ID-->
        <v-radio density="compact" label="Select Build by ID" value="buildById"></v-radio>
        <v-row>
          <v-col cols="12" class="pb-0">
            <v-text-field
              variant="outlined"
              density="compact"
              color="blue"
              v-model="buildValue"
              :rules="buildType === 'buildById' ? [required] : []"
              :disabled="buildType != 'buildById'"
              item-title="label"
              item-value="id"
              required
              ref="buildById"
            >
            </v-text-field>
          </v-col>
        </v-row>
      </v-radio-group>
      <!-- Agents -->
      <v-row>
        <v-col cols="12" class="pb-0">
          <v-select
            variant="outlined"
            density="compact"
            color="blue"
            v-model="agentsValues"
            :items="agents"
            label="Select Agent Groups"
            chips
            multiple
            clearable
          >
            <template v-slot:chip="{ item }">
              <v-chip
                color="blue"
                closable
                size="large"
                @mousedown.stop
                @click:close="deselectSuite('agents', item.title)"
              >
                {{ item.title }}
              </v-chip>
            </template>
          </v-select>
        </v-col>
      </v-row>
      <!-- Priority -->
      <v-row>
        <v-col cols="12">
          <v-select
            variant="outlined"
            density="compact"
            color="blue"
            v-model="priorityValue"
            :items="formattedPriorities"
            item-value="id"
            item-title="label"
            label="Select Priority"
            @update:model-value="selectChange"
          >
          </v-select>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" class="pt-0">
          <v-btn :disabled="!submitBtnActive" @click="submitJob" width="150" height="50" color="blue">Submit</v-btn>
        </v-col>
        <v-col cols="6" class="pt-0">
          <v-btn @click="selectNightly" elevated width="170" height="50" color="grey">SELECT NIGHTLY</v-btn>
        </v-col>
      </v-row>

      <v-snackbar v-model="snackbar" timeout="5000">
        <div class="text-h6"><strong>Submitted: </strong>{{ suitesValues.length }} job(s)</div>
        <template v-slot:actions>
          <v-btn
            color="primary"
            variant="text"
            @click="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>

    </v-container>
  </v-card>
</template>

<script>
export default {
  beforeMount() {
    this.initBuildsAndSuites();
    this.initJobConfigs();
    this.initAgentGroups();
  },
  computed: {
    submitBtnActive() {
      return !this.jobSubmissionInProgress && 
          this.agents.length > 0 &&
          this.configs.length > 0 && 
          this.builds.length > 0 &&
          this.suites.length > 0
    },
    required() {
      return (v) => {
        // Check if the value is truthy or if it's an array with length > 0
        let result = false;
        if (Array.isArray(v)) {
          result = (v.length > 0);
        } else {
          result = !!v;
        }
        return result || 'This field is required';
      };
    },
    formattedPriorities() {
      return this.priorities.map(priority => ({
        id: priority.id,
        label: `${priority.id} - ${priority.subtitle}`
      }));
    },
    formattedBuilds() {
      return this.builds.map(item => ({
        id: item.id,
        label: `${item.name} (${item.branch}) - (${item.tags})`
      }));
    },
  },
  data() {
    return {
      snackbar: false,
      configValue: '',
      configs: [],

      agentsValues: [],
      agents: [],

      buildType: 'buildBySelect',
      buildValue: null,
      builds: [],

      suitesValues: [],
      suites: [],
      priorities: [
        { id: 0, subtitle: "daily-default" },
        { id: 1, subtitle: "low" },
        { id: 2, subtitle: "high" },
        { id: 3, subtitle: "release-default" },
        { id: 4, subtitle: "urgent" },
      ],
      priorityValue: 1,
      jobSubmissionInProgress: false
    };
  },
  methods: {
    submitJob() {
      this.$refs.suite.validate();
      this.$refs.buildBySelect.validate();
      this.$refs.buildById.validate();
      if (this.suitesValues.length == 0 || this.buildValue == null) {
        return
      }

      const requestBody = {
          buildId: this.buildValue,
          suites: this.suitesValues,
          configId: this.configValue.id,
          agentGroups: this.agentsValues,
          priority: this.priorityValue
      };

      this.jobSubmissionInProgress = true;
      this.$axios.post("/api/newman/futureJob", requestBody)
        .then((response) => {
          this.jobSubmissionInProgress = false;
          this.snackbar = true;
        })
        .catch((error) => {
          this.jobSubmissionInProgress = false;
          console.error("Error:", error);
        });
    },

    selectNightly() {
     this.$axios.get("/api/newman/latest-builds?limit=1&tags=NIGHTLY&with-all-jobs-completed=true")
      .then((response) => {
        this.suitesValues = response.data.values[0].buildStatus.suitesIds
        .filter(id => {
          let item = this.suites.find(suite => suite.id == id)
          return !(item.name.startsWith("dev-") || item.name.startsWith("custom-"))
        })
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
    },
    selectChange(item) {
      console.log(item)
    },
    initBuildsAndSuites() {
     this.$axios.get("/api/newman/all-builds-and-suites")
        .then((response) => {
          this.suites = response.data.suites.map(item => {
            return {
              id: item.id,
              name: item.name
            }
          });
          this.builds = response.data.builds;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
      },

    initJobConfigs() {
     this.$axios.get("/api/newman/job-config")
      .then((response) => {
        this.configs = response.data.map(item => {
          return {
            id: item.id,
            name: item.name
          }
        });
        this.configValue = this.configs[this.configs.length-1];
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
    },

    initAgentGroups() {
     this.$axios.get("/api/newman/availableAgentGroups")
      .then((response) => {
        this.agents = response.data;
        this.agentsValues = this.agents;
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
      
    },
    deselectSuite(type, deselectItem) {
      // remove from the selected list the item that was just deselected
      switch (type) {
        case "suites":
          this.suitesValues = this.suitesValues.filter(
            (suite) => suite !== deselectItem
          );
          break;
        case "agents":
          this.agentsValues = this.agentsValues.filter(
            (agent) => agent !== deselectItem
          );
          break;
      }
    },
  },
};
</script>

<style scoped>
.v-menu > .v-overlay__content > .v-list > .v-list-item {
  display: flex;
  flex-direction: row;
  min-height: 30px;
  max-height: 30px;
}
</style>