import { createWebHistory, createRouter } from 'vue-router'

import JobsTable from './components/screens/Jobs.vue'
import Dashboard from './components/screens/Dashboard.vue'
import JobSubmit from './components/screens/JobSubmit.vue'
import Builds from './components/screens/Builds.vue'
import JobDetails from './components/screens/JobDetails.vue'
import BuildDetails from './components/screens/BuildDetails.vue'
import TestDetails from './components/screens/TestDetails.vue'
import Suites from './components/screens/Suites.vue'
import SuiteDetails from './components/screens/SuiteDetails.vue'
import History from './components/screens/History.vue'
import Agents from './components/screens/Agents.vue'
import Users from './components/screens/Users.vue'

const routes = [
  { path: '/', redirect: '/jobs', name: 'Landing', meta: { title: 'Jobs', icon: '' }},
  { path: '/jobs', name: 'Jobs', component: JobsTable, meta: { title: "Jobs View", icon: 'mdi-video-input-component' } },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard, meta: { title: "Dashboard", icon: 'mdi-view-dashboard'} },
  { path: '/submit', name: 'Submit', component: JobSubmit, meta: { title: "Submit Job", icon: 'mdi-play'} },
  { path: '/builds', name: 'Builds', component: Builds, meta: { title: "Builds", icon: 'mdi-domain'} },
  { path: '/suites', name: 'Suites', component: Suites, meta: { title: "Suites", icon: 'mdi-cards'} },
  { path: '/agents', name: 'Agents', component: Agents, meta: { title: "Agents", icon: 'mdi-face-man'} },
  { path: '/users', name: 'Users', component: Users, meta: { title: "Users", icon: 'mdi-account-multiple'} },
  { path: '/job/:id/:status', name: 'JobDetails', component: JobDetails, meta: { title: 'Job Details', icon: 'mdi-text-box-search-outline'}, props: true },
  { path: '/test/:id', name: 'TestDetails', component: TestDetails, meta: { title: 'Test Details', icon: 'mdi-text-box-search-outline'}, props: true },
  { path: '/build/:id', name: 'BuildDetails', component: BuildDetails, meta: { title: 'Build Details', icon: 'mdi-text-box-search-outline'}, props: true },
  { path: '/suite/:id', name: 'SuiteDetails', component: SuiteDetails, meta: { title: 'Suite Details', icon: 'mdi-text-box-search-outline'}, props: true },
  { path: '/test-history/:id', name: 'History', component: History, meta: { title: 'Test History', icon: 'mdi-history'}, props: true },
  {
    path: '/:pathMatch(.*)*', // Catch-all for undefined routes
    redirect: '/', // Redirect to the landing page
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
})

router.beforeEach((to, from, next) => {
    if (from.name === 'JobDetails' && to.name === 'JobDetails') {
      // do not change title of JobDetails when moving around
    } else {
      const defaultTitle = 'Newman';
      document.title = to.meta.title || defaultTitle;
    }
    next();
  });

export default router