<template>
  <v-card align="center">
    <div style="width: 80%">
      <v-card-title>
        <v-row dense justify="start" class="pa-0 ma-0">
          <v-col cols="3">
            <v-text-field
              v-model="search"
              density="compact"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              variant="outlined"
              hide-details
              single-line
              clearable
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-title>

      <v-data-table
        :search="search"
        density="compact"
        :headers="headers"
        :items="builds"
        class="buildsTable elevation-1"
        hover
        :loading="loading"
        :items-per-page="itemsPerPage"
        :items-per-page-options="[15, 20, 25, 50]"
      >
        <template v-slot:item.name="{ item }">
          <router-link class="font-bold" :to="{ name: 'BuildDetails', params: { id: item.id } }">
            {{ item.name }}
          </router-link>
        </template>

        <template v-slot:item.status="{ item }">
          <JobStatus :item="item" hide-running hide-total no-links align="center" />
        </template>
      </v-data-table>
    </div>
  </v-card>
</template>

<script>
import { parseBuildEntry } from '@/utils/utils';
import JobStatus from "../items/JobStatus.vue";

  export default {
    components: {
      JobStatus
    },
    data () {
      return {
        loading: true,
        itemsPerPage: 20,
        pullBuildsCount: 60,
        builds: [],
        search: null,
        headers: [
          { key: "name", title: "Build", width: "30%", minWidth: "100px", maxWidth: "100px"},
          { key: "tags", title: "Tags", width: "20%", minWidth: "10px", maxWidth: "100px"},
          { key: "id", title: "Id", width: "20%", minWidth: "100px", maxWidth: "100px"},
          { key: "buildTime", title: "Build Date", width: "15%", minWidth: "100px", maxWidth: "100px"},
          { key: "status", title: "Status", width: "25%", minWidth: "200px", maxWidth: "200px"},
        ],
      }
    },
    beforeMount() {
      this.initTable();
    },
    methods: {
      initTable() {
        this.$axios
          .get("/api/newman/build", {
            params: {
              limit: this.pullBuildsCount,
              orderBy: "-buildTime",
            },
          })
          .then((response) => {
            this.builds = response.data.values.map(parseBuildEntry); // Update the component's data
            this.loading = false;
          })
          .catch((error) => {
            console.error("Error fetching data:", error);
            this.loading = false;
          });
      }
    }
  }
</script>

