<template>
  <v-card align="center">
    <v-card-title align="start" style="width: 80%;" class="text-wrap">
      <h3> Test: {{ testDetails?.arguments }} </h3>
    </v-card-title>

    <v-container style="width: 100%; max-width: unset;">
      <div class="ml-10">
        <v-row class="my-0 mr-0" no-gutters v-for="(item, index) in info" :key="index">
          <v-col cols="2" align="start" class="font-weight-bold">{{ item.title }}:</v-col>
          <v-col cols="10" align="start" v-if="testDetails">
            <div v-if="item.key=='status'">
              <v-chip
                variant="elevated"
                class="text-uppercase font-weight-bold text-body-2 text-mono"
                :color="getStatusColor(testDetails[item.key])"
                :text="testDetails[item.key]"
                size="small"
                label
              ></v-chip>
            </div>
            <div v-else-if="item.key=='logs'" class="my-2">
              <div v-for="(path, key) in sortedLogs(item.key)" :key="key" class="log-tab my-0 font-weight-bold">
                <a :href="path" target="_blank">
                  &#x1F517; <span class="ml-2">{{ key }}</span>
                </a>
              </div>
            </div>
            <div v-else-if="item.key=='statsBranch' && testDetails[item.key]" class="my-0">
              {{ coloredHistoryStats(testDetails[item.key]) }} 
            </div>
            <div v-else-if="item.key=='statsMaster' && testDetails[item.key]" class="my-0">
              {{ coloredHistoryStats(testDetails[item.key], true) }}
            </div>
            <div v-else-if="item.key=='history'" class="my-0">
              <v-btn value="history"
                  prepend-icon="mdi-eye"
                  text="PREVIEW"
                  variant="outlined"
                  color="primary"
                  size="small"
                  class="font-bold"
                  :to="{ name: 'History', params: { id: item.id } }"
              ></v-btn>
            </div>
            <div v-else-if="item.key=='errorMessage'" class="my-0 preserve-whitespace">
              {{ (testDetails && testDetails[item.key]) || 'N/A' }}
            </div>
            <div v-else>
              {{ (testDetails && testDetails[item.key]) || 'N/A' }}
            </div>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </v-card>
</template>

<script setup>
import {getStatusColor} from '@/utils/utils';
</script>

<script>
import {parseJobTestEntry, parseJobTestEntryExtra, coloredHistoryStats} from '@/utils/utils';

export default {
  props: {
    id: String
  },
  beforeMount() {
    this.inittestDetails();
  },
  computed: {
    sortedLogs() {
      return (key) => {
        const logs = this.testDetails[key];
        return Object.entries(logs)
          .sort(([keyA], [keyB]) => keyA.localeCompare(keyB)) // Sort by key in ascending order
          .reduce((acc, [key, value]) => {
            acc[key] = value;
            return acc;
          }, {});
      };
    }
  },
  data() {
    return {
      testDetails: null,
      info: [
        { key: "status", title: "Status" },
        { key: "runNumber", title: "Run #" },
        { key: "id", title: "Id" },
        { key: "jobId", title: "Job Id" },
        { key: "arguments", title: "Arguments" },
        { key: "testType", title: "Test Type" },
        { key: "timeout", title: "Timeout" },
        { key: "errorMessage", title: "Error Message" },
        { key: "logs", title: "Logs" },
        { key: "assignedAgent", title: "Assigned Agent" },
        { key: "agentGroup", title: "Agent Group" },
        { key: "startTime", title: "Start Time" },
        { key: "endTime", title: "End Time" },
        { key: "scheduledAt", title: "Scheduled At" },
        { key: "history", title: "History" },
        { key: "statsBranch", title: "History Stats branch" }, // comes from 'historyStats'
        { key: "statsMaster", title: "History Stats master" }, // comes from 'historyStats'
      ],
    }
  },
  methods: {
    inittestDetails() {
      this.$axios.get(`/api/newman/test/${this.id}`)
        .then((response) => {
          this.testDetails = {...parseJobTestEntry(response.data), ...parseJobTestEntryExtra(response.data)}
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
  },
}
</script>

<style scoped>
.preserve-whitespace {
  white-space: pre-wrap;
}

.log-tab {
  margin-left: 20px;
}

.log-tab a {
  color: #1976d2;
  text-decoration: none;
}

.log-tab a:hover {
  text-decoration: underline;
}
</style>