<template>
  <v-card align="center" class="mx-auto">
    <v-row justify="center">
      <v-col cols="3">
        <v-card class="pa-4">
          <v-list
            align="left"
            max-height="800px"
            :items="users"
            item-value="name"
            lines="two"
            item-props
            slim
            density="dense"
            class="users-list ma-0 pa-0"
          >

            <template v-slot:prepend="{ item }">
              <v-icon :icon="item.icon" color="info" size="40"></v-icon>
            </template>
            <template v-slot:subtitle="{ item }">
              <strong v-html="item.role"></strong>
            </template>
            <template v-slot:title="{ item }">
              <h3 v-html="item.username"></h3>
            </template>
          
            <template v-slot:append="{ item }">
              <v-btn
                icon
                size="30"
                variant="elevated"
                @click="deleteUserPrompt(item)"
                color="#D30000"
              >
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </template>
          </v-list>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card class="pa-4">
          <v-text-field
            density="compact"
            label="Username"
            variant="outlined"
            v-model="newUser.username"
            class="mb-4"
            hide-details
            color="primary"
          ></v-text-field>
          <v-text-field
            density="compact"
            label="Password"
            type="password"
            variant="outlined"
            v-model="newUser.password"
            class="mb-4"
            hide-details
            color="primary"
          ></v-text-field>
          <v-text-field
            density="compact"
            label="Role"
            variant="outlined"
            v-model="newUser.role"
            class="mb-4"
            hide-details
            disabled
            color="primary"
          ></v-text-field>
          <div class="d-flex justify-end">
            <v-btn
              class="mt-2"
              color="primary"
              style="width: 30%;"
              
              @click="createUser"
            >
              Create
            </v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-card>

    <!-- Delete user -->
  <prompt-dialog
    ref="userDeleteDialog"
    prepend-icon="mdi-alert"
    icon-color="orange"
    title="Delete User"
    @promptDialog:onConfirm="confirmDeleteUser"
    @promptDialog:onCancel="cancelDeleteUser"
  >
    <dialog-prompt-body :text="deleteUserDialogBody" />
  </prompt-dialog>
  
  <v-snackbar v-model="snackbar" timeout="5000">
    <div class="text-h6">User has been {{ snackbarInfo.action }}: <strong>{{ snackbarInfo.username }}</strong></div>
    <template v-slot:actions>
      <v-btn
        color="primary"
        variant="text"
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import PromptDialog from '../modals/PromptDialog.vue';
import DialogPromptBody from '../modals/DialogPromptBody.vue';

export default {
  components: {
    PromptDialog,
    DialogPromptBody,
  },
  data() {
    return {
      users: [
        { type: 'subheader', title: 'Users' }
      ],
      newUser: {role: "user"},
      deleteUser: null,
      snackbarInfo: null,
      snackbar: false
    }
  },
  beforeMount() {
    this.initUsersList();
  },
  computed: {
    deleteUserDialogBody() {
      if (this.deleteUser == null) return "";

      return `Would you like to delete user <strong>${this.deleteUser.username}</strong> ?`;
    }
  },
  methods: {
    initUsersList() {
      this.$axios
        .get("/api/users/list")
        .then((response) => {
          this.users = response.data.flatMap((data, index, array) => {
            const user = {
              icon: "mdi-account-circle",
              username: data.username,
              role: data.role,
            };

            return index < array.length - 1
              ? [user, { type: 'divider', inset: true }]
              : [user];
          });
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    deleteUserPrompt(item) {
      this.deleteUser = item;
      this.$refs.userDeleteDialog.openDialog();
    },
    createUser() {
      this.snackbarInfo = {action: "created", username: this.newUser.username}
      this.$axios
        .post("/api/users/add", this.newUser)
        .then((response) => {
          this.snackbar = true;
          this.newUser = {role: "user"};
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    cancelDeleteUser() {},
    confirmDeleteUser() {
      this.snackbarInfo = {action: "removed", username: this.deleteUser.username}
      this.$axios
        .delete(`/api/users/${this.deleteUser.username}`)
        .then((response) => {
          this.snackbar = true;
          this.deleteUser = null;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    }
  }
}
</script>

<style>
.users-list .v-list-subheader {
  font-size: 24px;
  font-weight: 500;
  color: #1976d2; /* Custom color */
  text-transform: uppercase;
  padding: 10px 0;
  margin-left: 20px;
  margin-bottom: 10px;
  border-bottom: 1px solid #1976d2;
}
</style>