<template>
  <PromptDialog v-model="dialogVisible" @promptDialog:onConfirm="confirm">
    <template v-slot="modal">
      <v-container style="width: 90%" justify-center>
        <v-card-text v-if="text" class="mt-5">
          <div v-html="formattedText"></div>
        </v-card-text>

        <v-row no-gutters class="mt-5">
          <span>Set a new name for the copied suite:</span>
        </v-row>

        <v-row no-gutters class="mt-5">
          <v-text-field
            color="primary"
            v-model="visibleSuiteNameValue"
            label="Suite name"
            variant="outlined"
            hide-details
            density="comfortable"
            @input="modal.onValidate(isSuiteNameValid())"
          >
            <template #prepend-inner>
              <span style="color: grey">dev-</span>
            </template>
          </v-text-field>
        </v-row>
      </v-container>
    </template>
  </PromptDialog>
</template>

<script>
import PromptDialog from './PromptDialog.vue';

export default {
  components: {
    PromptDialog
  },
  props: {
    text: { type: String }
  },
  watch: {
    visibleSuiteNameValue(newValue) {
      this.modelSuiteNameValue = 'dev-' + newValue;
    }
  },
  data() {
    return {
      dialogVisible: false,
      visibleSuiteNameValue: '',
      modelSuiteNameValue: ''
    }
  },
  computed: {
    formattedText() {
      return this.text.replace(/\n/g, '<br>'); // Replace new line with <br>
    },
  },
  methods: {
    input() {
      console.log("emit data: " + this.modelSuiteNameValue)
    },
    isSuiteNameValid() {
      return this.modelSuiteNameValue.startsWith('dev-') && this.visibleSuiteNameValue.length > 2;
    },
    open(suite) {
      this.visibleSuiteNameValue = `copy-of-${suite}`;
      this.dialogVisible = true;
    },
    confirm() {
      this.$emit("copySuite:onConfirm", this.modelSuiteNameValue)
    }
  }
}
</script>
