<template>
  <v-card align="center">
    <div style="width: 80%">
      <v-card-title align="start" class="text-wrap">
        <div>
          <h3> History: </h3>
          <h3 class="ml-5 font-weight-regular"> {{ test }} </h3>
        </div>
        <v-row dense justify="start" class="pa-0 ma-0">
          <v-col cols="3">
            <v-text-field
              v-model="search"
              density="compact"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              variant="outlined"
              hide-details
              single-line
              clearable
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-title>

      <v-data-table
        :search="search"
        density="compact"
        :headers="headers"
        :items="items"
        class="historyTable elevation-1"
        hover
        :loading="loading"
        :items-per-page="itemsPerPage"
        :items-per-page-options="[15, 20, 25, 50]"
      >

        <template v-slot:item.testId="{ item }">
          <router-link class="font-bold" :to="{ name: 'TestDetails', params: { id: item.testId } }">
            {{ item.testId }}
          </router-link>
        </template>
        
        <template v-slot:item.jobId="{ item }">
          <router-link class="font-bold" :to="{ name: 'JobDetails', params: { id: item.jobId, status: 'ALL'} }">
            {{ item.jobId }}
          </router-link>
        </template>

        <template v-slot:item.build="{ item }">
          <router-link class="font-bold" :to="{ name: 'BuildDetails', params: { id: item.build.id } }">
            {{ item.build.name }}
          </router-link>
        </template>

        <template v-slot:item.state="{ item }">
          <div>
            <v-chip
              variant="elevated"
              class="text-uppercase state-chip"
              :color="getStatusColor(item.state)"
              :text="item.state"
              size="small"
              label
            ></v-chip>
          </div>
        </template>

      </v-data-table>
    </div>
  </v-card>
</template>

<script setup>
import { getStatusColor } from '@/utils/utils';
</script>

<script>
import { parseTestHistoryEntry } from '@/utils/utils';

export default {
  props: {
    id: String
  },
  data() {
    return {
      loading: true,
      itemsPerPage: 20,
      pullBuildsCount: 60,
      items: [],
      search: null,
      test: null,
      headers: [
        { key: "testId", title: "Test Id", width: "15%", minWidth: "175px", maxWidth: "175px"},
        { key: "jobId", title: "Job Id", width: "15%", minWidth: "175px", maxWidth: "175px"},
        { key: "build", title: "Build", width: "17%", minWidth: "150px", maxWidth: "150px"},
        { key: "endTime", title: "End Time", width: "15%", minWidth: "140px", maxWidth: "140px"},
        { key: "duration", title: "Duration", width: "10%", minWidth: "90px", maxWidth: "90px"},
        { key: "runNum", title: "Run #", width: "5%", minWidth: "50px", maxWidth: "50px"},
        { key: "agentId", title: "Agent Id", width: "15%", minWidth: "100px", maxWidth: "100px"},
        { key: "agentGroup", title: "Agent Group", width: "10%", minWidth: "100px", maxWidth: "100px"},
        { key: "state", title: "State", width: "5%", minWidth: "100px", maxWidth: "100px"},
      ]
    }
  },
  beforeMount() {
    this.initTable();
  },
  methods: {
    initTable() {
      this.$axios
        .get("/api/newman/test-history", {
          params: {
            id: this.id
          },
        })
        .then((response) => {
          this.items = response.data.values.map(parseTestHistoryEntry); // Update the component's data
          this.loading = false;
          this.test = response.data.values[0].test.arguments.join(" ")
          console.log(this.entries)
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    }
  }
}
</script>

