function countAgentsNum(item) {
    if (item.preparingAgents && item.preparingAgents.length > 0) {
      return item.preparingAgents.length;
    } else if (item.agents && item.agents.length > 0){
      return item.agents.length;
    } else {
      return "-";
    }
  }

function consolidateBuildName(item) {
    if (item.build) {
      return `${item.build.name} (${item.build.branch})`;
    } else {
      return `${item.buildName} (${item.buildBranch})`;
    }
  }

function calculateSubmittedTime(timestamp) {
    const currentTime = Date.now();
    const timeDifference = currentTime - timestamp;
    let result = "";

    const minutesPassed = Math.floor(timeDifference / (1000 * 60)); // Minutes
    const hoursPassed = Math.floor(timeDifference / (1000 * 60 * 60)); // Hours
    const daysPassed = Math.floor(timeDifference / (1000 * 60 * 60 * 24)); // Days

    if (daysPassed > 0) {
      result = daysPassed + " days ago";
    } else if (hoursPassed > 0) {
      result = hoursPassed + " hours ago";
    } else if (minutesPassed > 0) {
      result = minutesPassed + " minutes ago";
    } else {
      result = 'just now'
    }

    return { ago: result, dateTime: toLocaleFormatted(timestamp) };
  }

function calculateDuration(startTime, endTime) {
    if (!startTime) return "";
    // If `endTime` is not provided, use the current time
    endTime = endTime || Date.now();

    // Calculate the difference in milliseconds
    const durationMs = endTime - startTime;

    // Convert to minutes, hours, and days
    const minutes = Math.floor(durationMs / (1000 * 60)) % 60;
    const hours = Math.floor(durationMs / (1000 * 60 * 60)) % 24;
    const days = Math.floor(durationMs / (1000 * 60 * 60 * 24));

    // Format the output
    if (days > 0) {
      return `${days}d, ${hours}h`;
    } else {
      return `${hours}h, ${minutes.toString().padStart(2, "0")}m`;
    }
  }

  function calculateDurationMinsSecs(startTime, endTime) {
    if (!startTime) return "";
    // If `endTime` is not provided, use the current time
    endTime = endTime || Date.now();

    // Calculate the difference in milliseconds
    const durationMs = endTime - startTime;

    // Convert to minutes, hours, and days
    const minutes = Math.floor(durationMs / (1000 * 60)) % 60;
    const seconds =  Math.floor(durationMs / 1000);

    // Format the output
    if (minutes < 1 ) {
      return `${seconds} seconds`;
    } else if (minutes == 1) {
      return `${minutes} minute`;
    } else {
      return `${minutes} minutes`;
    }
  }

export function parseJobEntry(item) {
  return {
      state: item.state, // Assign value or null if missing
      progress: ((item.passedTests + item.failedTests) / item.totalTests) * 100, // Set to null as per your requirement
      jobId: item.id, // Assign value or null if missing
      buildId: item.buildId || item.build.id, // Assign value or null if missing
      build: item.buildName || item.build.name,
      buildConsolidated: consolidateBuildName(item), // Build string concatenation
      suite: item.suiteName || item.suite.name, // Assign value or null if missing
      jdk: item.jobConfigName || item.jobConfig.name, // Assign value or null if missing
      duration: calculateDuration(item.startTime, item.endTime), // Set to null as per your requirement
      submittedAt: calculateSubmittedTime(item.submitTime), // Set to null as per your requirement
      submittedBy: item.submittedBy || null, // Assign value or null if missing
      agentsNum: countAgentsNum(item), // Count of agent groups
      agentGroups: item.agentGroups || null, 
      priority: item.priority, // Assign value or null if missing
      // runningTests: item.runningTests,
      status: [
        item.runningTests || 0,
        item.passedTests || 0,
        item.failedTests || 0,
        item.failed3TimesTests || 0,
        item.totalTests || 0,
      ]
    }
}

export function parseJobEntryExtra(item) {
  return {
    startTime: (item.startTime && toLocaleFormatted(item.startTime)) || 'N/A',
    endTime: (item.endTime && toLocaleFormatted(item.endTime)) || 'N/A',
    preparingAgents: item.preparingAgents?.length || '0',
    submittedAt: toLocaleFormatted(item.submitTime),
    jobSetupLogs: item.jobSetupLog?.agentLogs
  }
}

export function toLocaleFormatted(time) {
  const formattedDate = new Date(time).toLocaleString('en-US', {
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: false, // 24-hour format
  });
  return formattedDate
}

export function parseJobTestEntry(item) {
  let splitIndex = null;
  let showMasterBranch = true;
  if (item.historyStats) {
    splitIndex = item.historyStats.indexOf("_");  // let find out wether master stats is present
    if (splitIndex == -1) {
      splitIndex = item.historyStats.length   // master is not present, show entire sequence for current branch
      showMasterBranch = false;
    }
  }
  return {
      agentGroup: item.agentGroup,
      arguments: item.arguments,
      assignedAgent: item.assignedAgent,
      errorMessage: item.errorMessage,
      id: item.id,
      jobId: item.jobId,
      name: item.name,
      progressPercent: item.progressPercent,
      runNumber: item.runNumber,
      startTime: toLocaleFormatted(item.startTime),
      endTime: toLocaleFormatted(item.endTime),
      scheduledAt: toLocaleFormatted(item.scheduledAt),
      status: item.status,
      testScore: item.testScore,
      statsBranch: splitIndex ? item.historyStats.slice(0, splitIndex).trim() : null, // show prior symbol '_' or full length
      statsMaster: (splitIndex && showMasterBranch) ? item.historyStats.slice(splitIndex+1).trim() : null,   // show after '_' if exists
      duration: calculateDurationMinsSecs(item.startTime, item.endTime)
    }
}

export function parseJobTestEntryExtra(item) {
  return {
      id: item.id,
      timeout: item.timeout,
      testType: item.testType,
      logs: item.logs?.testLogs,
      arguments: item.arguments.join(" ")
    }
}

export function parseBuildEntry(item) {
  return {
    id: item.id,
    name: item.name ,
    xap: item.shas && item.shas['xap-open'] || null,
    xappremium: item.shas?.xap,
    branch: item.branch || null,
    resources: item.resources || null,
    testsMetadata: item.testsMetadata || null,
    tags: item.tags?.filter(tag => tag.length > 0).join(", "),
    buildTime: toLocaleFormatted(item.buildTime),
    status: [
      item.buildStatus.runningTests || 0,
      item.buildStatus.passedTests || 0,
      item.buildStatus.failedTests || 0,
      item.buildStatus.failed3TimesTests || 0,
      item.buildStatus.totalTests || 0,
    ]
  }
}

export function parseSuiteEntry(item) {
  return {
    id: item.id,
    name: item.name,
    customVariables: item.customVariables,
    requirements: item.requirements,
    criteria: JSON.stringify(item.criteria, null, 4)
  }
}

export function parseTestHistoryEntry(item) {
  return {
    testId: item.test.id,
    jobId: item.job.id,
    build: {id: item.job.buildId, name: item.job.buildName},
    endTime: toLocaleFormatted(item.test.endTime),
    duration: calculateDuration(item.test.startTime, item.test.endTime),
    runNum: item.test.runNumber,
    agentId: item.test.assignedAgent,
    agentGroup: item.test.agentGroup,
    state: item.test.status
  }
}

export function parseAgentEntry(item) {
  return {
    action: item.action,
    capabilities: item.capabilities.join(", "),
    currentTests: item.currentTests,
    host: item.host,
    hostAddress: item.hostAddress,
    id: item.id,
    job: item.job,
    jobId: item.jobId,
    lastSeen: toLocaleFormatted(item.lastTouchTime),
    name: item.name,
    pid: item.pid,
    retries: item.setupRetries || 0,
    state: item.state,
    group: item.groupName
  }
}

export function getStatusColor(status) {
  switch(status) {
    case 'RUNNING':
      return 'blue'
    case 'FAIL':
      return '#D30000'
    case 'READY':
    case 'PENDING':
    case 'IDLING':
      return 'grey'
    case 'BROKEN':
      return '#D30000';
    case 'SUCCESS':
    case 'DONE':
      return 'green'
    case 'PAUSED':
      return 'orange'
    default:
      return 'yellow'
  }
}

export function coloredHistoryStats(stats, isSquare) {
  if (stats == null) return null;

  let result = stats.replace(/\s/g, '') // replace spaces
          .slice(0,10)            // leave 10 last statuses
          .replace(/\./g, isSquare ? '' : '')   // replace SUCCESS
          .replace(/\|/g, isSquare ? '' : '');  // replace FAIL
  let circlesCount = result.length / 2; // 1 circle takes 2 chars
  return result.padEnd(circlesCount+10, isSquare ? '' : '');
}