<template>
  <v-card-title>
    <v-row>
      <v-col cols="4">
        <v-row dense align="center">
          <v-col cols="1">
            <v-menu location="right">
              <template v-slot:activator="{ props }">
                <v-btn
                  v-bind="props"
                  size="medium"
                  icon="mdi-view-list"
                  flat
                  :disabled="selected.length < 1"
                  style="border-radius: 0"
                ></v-btn>
              </template>
              <v-list>
                <v-list-item slim dense key="title">
                  <v-list-item-title class="list-header">
                    ({{ selected.length }} selected)
                  </v-list-item-title>
                </v-list-item>
                <v-list-item
                  class="select-list-item"
                  @click="selectedPause"
                  slim
                  density="compact"
                  prepend-icon="mdi-pause"
                  :disabled="!selectedPauseVisible"
                  value="pause"
                  base-color="orange"
                  title="Pause"
                />
                <v-list-item
                  class="select-list-item"
                  @click="selectedResume"
                  slim
                  density="compact"
                  prepend-icon="mdi-play"
                  :disabled="!selectedResumeVisible"
                  value="resume"
                  base-color="green"
                  title="Resume"
                />
                <v-divider style="display: block"></v-divider>
                <v-list-item
                  class="select-list-item"
                  @click="deleteJobPrompt(selected)"
                  slim
                  density="compact"
                  prepend-icon="mdi-delete"
                  :disabled="!selectedDeleteVisible"
                  value="delete"
                  base-color="#D30000"
                  title="Delete"
                />
                <v-list-item
                  class="select-list-item"
                  @click="showHideSelected"
                  slim
                  density="compact"
                  prepend-icon="mdi-eye"
                  value="hide-show"
                  :title="hiddenMode ? 'Show all' : 'Hide not selected'"
                />
              </v-list>
            </v-menu>
          </v-col>
          <v-col cols="7">
            <v-text-field
              v-model="search"
              density="compact"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              variant="outlined"
              hide-details
              single-line
              clearable
            ></v-text-field>
          </v-col>
          <v-col cols="4" class="d-block">
            <div class="d-block">
              <v-btn
                class="mr-1 font-bold"
                style="min-width: 10px"
                color="#D30000"
                @click="search = 'BROKEN'"
                >B</v-btn
              >
              <v-btn
                class="mr-1 font-bold"
                style="min-width: 10px"
                color="blue"
                @click="search = 'RUNNING'"
                >R</v-btn
              >
              <v-btn
                class="mr-1 font-bold"
                style="min-width: 10px"
                color="orange"
                @click="search = 'PAUSED'"
                >P</v-btn
              >
              <v-btn color="grey" class="font-bold" variant="outlined" @click="search = ''"
                >ALL</v-btn
              >
            </div>
          </v-col>
        </v-row>
      </v-col>
      <v-spacer></v-spacer>
      <v-col cols="3" class="ml-auto mb-n1 mt-n1">
        <v-row dense align="center" justify="end">
          <v-col cols="10" v-if="!noJobsCount">
            <div class="d-flex align-center" style="gap: 8px;">
              <div>Jobs count:</div>
              <v-text-field
                counter
                color="primary"
                type="number"
                v-model="pullJobsCount"
                density="compact"
                variant="outlined"
                hide-details
                single-line
                minWidth="85px"
                @keyup.enter="initTable(false)"
              ></v-text-field>
              <v-btn color="primary" class="font-bold" @click="initTable(false)">Apply</v-btn>
            </div>
          </v-col>
          <v-col cols="1" align="right">
            <v-menu location="start" :close-on-content-click="false">
              <template v-slot:activator="{ props }">
                <v-btn
                  color="primary"
                  v-bind="props"
                  size="default"
                  icon="mdi-table-headers-eye"
                  variant="elevated"
                  rounded
                  minHeight="35px"
                  minWidth="35px"
                  maxHeight="35px"
                  maxWidth="35px"
                ></v-btn>
              </template>
              <v-list density="compact">
                <v-list-item-title class="list-header"
                  >Columns</v-list-item-title
                >
                <v-divider></v-divider>
                <v-list-item
                  class="d-flex"
                  v-for="i in headers.length - 2"
                  :key="i"
                >
                  <v-list-item-action>
                    <v-checkbox
                      density="dense"
                      v-model="columnVisibility[headers[i].key]"
                      :label="headers[i].title"
                      hide-details
                      class="ml-auto"
                      :disabled="headers[i].key == 'buildConsolidated'"
                      @change="saveColumnsState"
                    ></v-checkbox>
                  </v-list-item-action>
                </v-list-item>
              </v-list>
            </v-menu>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-card-title>
  <!-- Main data table -->
  <v-data-table
    :search="search"
    density="compact"
    :headers="visibleHeaders"
    v-model="selected"
    :items="filteredItems"
    item-value="jobId"
    class="jobsTable elevation-1"
    hover
    :show-select="!noSelect"
    disable-sort
    :row-props="setRowClass"
    :loading="loading"
    :custom-filter="customFilter"
    :items-per-page="itemsPerPage"
    @update:items-per-page="updateItemsPerPage"
    :items-per-page-options="[15, 20, 25, 50]"
  >
    <template v-slot:item.state="{ item }">
      <div>
        <v-chip
          variant="elevated"
          class="text-uppercase state-chip"
          :color="getStatusColor(item.state)"
          :text="item.state"
          size="small"
          label
        ></v-chip>
      </div>
    </template>

    <template v-slot:item.progress="{ item }">
      <v-progress-linear
        color="#5bc0de"
        height="25"
        :model-value="item.progress"
        :striped="item.state == 'RUNNING'"
        rounded="lg"
      >
        <template v-slot:default="{ value }">
          <strong>{{ Math.ceil(value) }}%</strong>
        </template>
      </v-progress-linear>
    </template>

    <template v-slot:item.jobId="{ item }">
      <span v-tooltip="getTooltipConfig(item.jobId)">
        <router-link
          class="font-bold"
          :to="{
            name: 'JobDetails',
            params: { id: item.jobId, status: 'ALL' },
          }"
        >
          {{ item.jobId }}
        </router-link>
      </span>
    </template>

    <template v-slot:item.suite="{ item }">
      <span v-tooltip="getTooltipConfig(item.suite)" class="font-bold">
        {{ item.suite }}
      </span>
    </template>

    <template v-slot:item.jdk="{ item }">
      <div align="center">
        <span class="font-bold" v-tooltip:bottom="getTooltipConfig(item.jdk)">
          <v-chip
            prepend-icon="mdi-language-java"
            variant="tonal"
            class="text-uppercase font-weight-bold"
            :text="item.jdk.replace(/\D/g, '')"
            size="small"
            label
            outlined
          ></v-chip>
        </span>
      </div>
    </template>

    <template v-slot:item.duration="{ item }">
      <div class="font-bold">
        {{ item.duration }}
      </div>
    </template>

    <template v-slot:item.submittedBy="{ item }">
      <div class="font-bold">
        {{ item.submittedBy }}
      </div>
    </template>

    <template v-slot:item.agentsNum="{ item }">
      <div class="font-bold">
        {{ item.agentsNum }}
      </div>
    </template>

    <template v-slot:item.submittedAt="{ item }">
      <span
        class="font-bold"
        style="margin-left: 10px"
        v-tooltip="getTooltipConfig(item.submittedAt.dateTime)"
      >
        {{ item.submittedAt.ago }}
      </span>
    </template>

    <template v-slot:item.buildConsolidated="{ item }">
      <span v-tooltip="getTooltipConfig(item.buildConsolidated)">
        <router-link
          class="font-bold"
          :to="{ name: 'BuildDetails', params: { id: item.buildId } }"
        >
          {{ item.buildConsolidated }}
        </router-link>
      </span>
    </template>

    <template v-slot:item.agentGroups="{ item }">
      <span
        class="font-bold"
        v-tooltip="getTooltipConfig(agentGroupsFormatted(item.agentGroups))"
      >
        {{ agentGroupsFormatted(item.agentGroups) }}
      </span>
    </template>

    <template v-slot:item.priority="{ item }">
      <div class="font-bold">
        {{ item.priority }}
      </div>
    </template>

    <!-- Multi color statuses -->
    <template v-slot:item.status="{ item }">
      <JobStatus :item="item" align="center" />
    </template>

    <!-- buttons - PLAY, DELETE, CONFIG -->
    <template v-slot:item.config="{ item }">
      <div class="ml-3" align="center">
        <!-- PLAY -->
        <v-btn
          tile
          class="mr-1 text-white"
          height="26px"
          width="26px"
          rounded
          :icon="getPlayBtnIcon(item.state)"
          :color="getPlayBtnColor(item.state)"
          :disabled="pauseBtnDisabledStates.includes(item.state)"
          @click="startPauseToggle(item.jobId)"
        />
        <!-- DELETE (DISABLED if not: BROKEN, DONE, PAUSED && runningTests == 0)-->
        <v-btn
          tile
          icon="mdi-delete"
          height="26px"
          width="26px"
          rounded
          color="#D30000"
          class="mr-1"
          :disabled="!(pauseBtnDisabledStates.includes(item.state) || item.state == 'PAUSED') || item.runningTests > 0"
          @click="deleteJobPrompt(item)"
        />
        <!-- CONFIG -->
        <v-btn
          tile
          icon="mdi-cog"
          height="26px"
          width="26px"
          rounded
          :disabled="item.state == 'DONE'"
          @click="configureJobPrompt(item)"
        />
      </div>
    </template>
  </v-data-table>

  <!-- Delete Job -->
  <prompt-dialog
    ref="jobDeleteDialog"
    prepend-icon="mdi-alert"
    icon-color="orange"
    title="Delete Job"
    @promptDialog:onConfirm="confirmDeleteJob"
    @promptDialog:onCancel="cancelDeleteJob"
    :disabled="dialogDisabled"
  >
    <dialog-prompt-body :text="deleteSelectedJobsDialogBody" />
  </prompt-dialog>

  <!-- Config Job -->
  <prompt-dialog
    ref="jobConfigDialog"
    prepend-icon="mdi-pencil-box"
    :title="configItem && `Edit: <strong>${configItem.suite} [${configItem.jobId}]</strong>`"
    @promptDialog:onConfirm="confirmConfigureJob"
    @promptDialog:onCancel="cancelConfigureJob"
    :disabled="dialogDisabled"
    :width="700"
  >
    <dialog-content-config-job :job="configItem"
    ></dialog-content-config-job>
  </prompt-dialog>
</template>

<script setup>
import { getStatusColor } from "@/utils/utils";
</script>

<script>
import { inject } from "vue";
import { parseJobEntry } from "@/utils/utils";
import PromptDialog from "../modals/PromptDialog.vue";
import DialogPromptBody from "../modals/DialogPromptBody.vue";
import DialogContentConfigJob from "../modals/DialogConfigJob.vue";
import JobStatus from "./JobStatus.vue";
import StorageHelper from '../../service/StorageHelper';

export default {
  components: {
    PromptDialog,
    DialogPromptBody,
    DialogContentConfigJob,
    JobStatus,
  },
  props: {
    noJobsCount: { type: Boolean, default: false },
    noSelect: { type: Boolean, default: false },
    noColumnsSelect: { type: Boolean, default: false },
    buildId: String,
  },
  watch: {
    jobUpdate(job) {
      if (job && !this.loading) {
        const updatedJob = parseJobEntry(job);
        
        if (this.buildId && updatedJob.buildId != this.buildId) return; // do not update jobs not related to current buidlId if given
        
        const existingJobIndex = this.items.findIndex(
          (item) => item.jobId === job.id
        );
        if (existingJobIndex !== -1) {
          this.items[existingJobIndex] = updatedJob;  // Update only the specific item without affecting the rest
        } else {
          this.items.unshift(updatedJob);
        }
      }
    },
    selected(content) {
      // turn off hidden mode in case no items selected
      if (content.length == 0 && this.hiddenMode) {
        this.hiddenMode = false;
      }
    },
  },
  beforeMount() {
    this.initTable(true);
    this.jobUpdate = inject("job-update");
  },
  mounted() {
    const storedColumns = localStorage.getItem("columns");
    if (storedColumns) {
      this.columnVisibility = JSON.parse(storedColumns);
    } else {
      this.headers.forEach((header) => {
        this.columnVisibility[header.key] = true; // All columns visible by default
      });
    }

    window.addEventListener('beforeunload', this.handleBeforeUnload)  // Refresh page
  },
  data() {
    return {
      // host: import.meta.env.VITE_PROTOCOL + import.meta.env.VITE_HOST,
      jobUpdate: null,
      loading: true,
      search: "",
      selected: [],
      pauseBtnDisabledStates: ["BROKEN", "DONE"],
      headers: [
        { key: "state", title: "State", width: "5%", minWidth: "75px", maxWidth: "75px" },
        { key: "progress", title: "Progress", width: "5%", maxWidth: "100px" },
        { key: "jobId", title: "Job Id", width: "5%", maxWidth: "100px" },
        { key: "suite", title: "Suite", width: "20%", maxWidth: "100px" },
        { key: "jdk", title: "JDK", width: "30px" },
        { key: "duration", title: "Duration", width: "5%", maxWidth: "80px" },
        { key: "submittedAt", title: "Submitted at", width: "7.5%", maxWidth: "150px" },
        { key: "buildConsolidated", title: "Build", width: "30%", maxWidth: "100px" },
        { key: "submittedBy",title: "Submitter",width: "7%",maxWidth: "70px" },
        { key: "agentsNum", title: "#agents", width: "7%", maxWidth: "50px" },
        { key: "agentGroups",title: "Agent Groups",width: "5%",maxWidth: "130px" },
        { key: "priority", title: "Priority", width: "10%", maxWidth: "65px" },
        { key: "status",title: "Status",minWidth: "240px",maxWidth: "240px" },
        { key: "config", width: "50px" },
      ],
      items: [],
      configItem: null,
      deleteItem: null,
      dialogDisabled: true,
      hiddenMode: false,
      pullJobsCount: 60,
      itemsPerPage: localStorage.getItem("items_per_page") || 20,
      columnVisibility: {},
    };
  },
  computed: {
    filteredItems() {
      if (this.hiddenMode && this.selected.length > 0) {
        return this.items.filter((item) => this.selected.includes(item.jobId));
      }
      return this.items;
    },
    deleteSelectedJobsDialogBody() {
      if (this.deleteItem == null) return "";

      if (Array.isArray(this.deleteItem)) {
        return `Would you like to delete <strong>${this.deleteItem.length} jobs</strong> ?`;
      } else {
        return `Would you like to delete job\n<strong>${this.deleteItem.suite} [${this.deleteItem.jobId}]</strong> ?`;
      }
    },
    selectedDeleteVisible() {
      if (this.selected.length == 0) return false;
      const selectedItems = this.items.filter((item) =>
        this.selected.includes(item.jobId)
      );
      return selectedItems.every(
        (item) => item.state == "PAUSED" || item.state == "DONE"
      );
    },
    selectedResumeVisible() {
      if (this.selected.length == 0) return false;
      const selectedItems = this.items.filter((item) =>
        this.selected.includes(item.jobId)
      );
      return selectedItems.every((item) => item.state == "PAUSED");
    },
    selectedPauseVisible() {
      if (this.selected.length == 0) return false;
      const selectedItems = this.items.filter((item) =>
        this.selected.includes(item.jobId)
      );
      return selectedItems.every(
        (item) => item.state == "RUNNING" || item.state == "READY"
      );
    },
    visibleHeaders() {
      return this.headers.filter((header) => this.columnVisibility[header.key]);
    },
  },
  methods: {
    initTable(onMount) {
      this.loading = true;
      if (this.buildId) {
        this.requestBuildDetailsView();
      } else {
        if (StorageHelper.hasRawJobsSaved() & onMount) {
          this.items = StorageHelper.getRawJobs().map(parseJobEntry);
          this.pullJobsCount = this.items.length;
          this.loading = false;
        } else {
          this.requestFullJobsView();
        }
      }
    },
    requestFullJobsView() {
      console.log("request full view")
      this.$axios
        .get("/api/newman/jobs-view", {
          params: {
            limit: this.pullJobsCount,
            orderBy: "-submitTime",
          },
        })
        .then((response) => {
          StorageHelper.saveRawJobs(response.data.values);
          this.items = response.data.values.map(parseJobEntry); // Update the component's data
          this.loading = false;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    },
    requestBuildDetailsView() {
      console.log("request build view")
      this.$axios
        .get("/api/newman/job", {
          params: {
            buildId: this.buildId,
            all: true,
          },
        })
        .then((response) => {
          this.items = response.data.values
              .map(parseJobEntry)
              .sort((a, b) => b.status[3] - a.status[3]); // sort to keep most failures on most top
          this.loading = false;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    },
    customFilter(item, searchInput, metadata) {
      let result = false;

      if (searchInput.length < 3) return true;

      const searchArray = searchInput.includes(",")
        ? searchInput
            .split(",")
            .map((term) => term.trim())
            .filter(Boolean)
        : [searchInput.trim()];

      const excludeFields = ["data-table-select", "config", "agentGroups"];
      const entry = metadata.columns;
      for (const search of searchArray) {
        for (const key in entry) {
          if (excludeFields.includes(key)) {
            continue;
          }
          if (key === "submittedAt" && entry[key].dateTime.includes(search)) {
            result = true;
            break;
          }
          const value = String(entry[key]);
          if (value.toLowerCase().includes(search.toLowerCase())) {
            result = true;
            break;
          }
        }
        if (result) break;
      }

      return result;
    },
    setRowClass(item) {
      let statuses = item.item.status;
      let activateRow = statuses[1] == statuses[4];
      let isDark = this.$vuetify.theme.current.dark;
      return {
        class: {
          "succeed-row-light": activateRow && !isDark,
          "succeed-row-dark": activateRow && isDark,
        },
      };
    },
    getTooltipConfig(item_field) {
      return {
        location: "bottom",
        text: item_field,
        openDelay: 1000,
      };
    },
    getPlayBtnColor(state) {
      switch (state) {
        case "RUNNING":
        case "DONE":
        case "BROKEN":
        case "READY":
          return "orange";
        case "PAUSED":
          return "green";
      }
    },
    getPlayBtnIcon(state) {
      switch (state) {
        case "RUNNING":
        case "DONE":
        case "BROKEN":
        case "READY":
          return "mdi-pause";
        case "PAUSED":
          return "mdi-play";
      }
    },
    startPauseToggle(jobId) {
      this.$axios
        .post(`/api/newman/job/${jobId}/toggle`)
        .then((response) => {
          const respItem = parseJobEntry(response.data);
          const jobIndex = this.items.findIndex(
            (item) => item.jobId === respItem.jobId
          );
          if (jobIndex !== -1) {
            // Replace the old item with the new one
            this.items[jobIndex] = respItem;
          } else {
            console.log("Job not found");
          }
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    configureJobPrompt(item) {
      this.dialogDisabled = false;
      this.requestAgentsGroup().then((agentGroups) => {
        this.configItem = Object.assign({}, {
          ref: item,
          suite: item.suite,
          jobId: item.jobId,
          build: item.build,
          priority: item.priority,
          agentGroups: item.agentGroups, //assign response here
          allAgentGroups: agentGroups,
        });
        this.$refs.jobConfigDialog.openDialog();
      });
    },
    deleteJobPrompt(item) {
      this.dialogDisabled = false;
      this.deleteItem = item;
      this.$refs.jobDeleteDialog.openDialog();
    },
    confirmDeleteJob() {
      this.dialogDisabled = true;
      if (Array.isArray(this.deleteItem)) {
        this.selectedDelete();
        //this.deleteItem = null;
      } else {
        this.$axios
          .delete(`/api/newman/job/${this.deleteItem.jobId}`)
          .then((response) => {
            const jobIndex = this.items.findIndex(
              (item) => item.jobId === this.deleteItem.jobId  // find item among others
            );
            if (jobIndex !== -1) {
              this.items.splice(jobIndex, 1);   // delete it by the index
            } else {
              console.info("Job not found");
            }
            // this.deleteItem = null;
            this.selected = [];
          })
          .catch((error) => {
            // this.deleteItem = null;
            console.error("Error fetching data:", error);
          });
      }
    },
    cancelDeleteJob() {
      this.dialogDisabled = true;
      this.deleteItem = null;
    },
    confirmConfigureJob() {
      this.dialogDisabled = true;
      this.configItem.allAgentGroups = null; // we don't need this to be sent to the server. For local use only
      this.$axios
        .post(`/api/newman/job/${this.configItem.jobId}/edit`, this.configItem)
        .then((response) => {
          // this.configItem = null;
        })
        .catch((error) => {
          // this.configItem = null;
          console.error("Error fetching data:", error);
        });
    },
    cancelConfigureJob() {
      this.dialogDisabled = true;
      this.configItem = null;
    },
    selectedPause() {
      this.$axios
        .post("/api/newman/jobs/pause", this.selected)
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    selectedResume() {
      this.$axios
        .post("/api/newman/jobs/resume", this.selected)
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    selectedDelete() {
      this.$axios
        .post("/api/newman/jobs/deletejobs", this.selected)
        .then((response) => {
          const jobsToRemove = new Set(this.selected)
          this.items = this.items.filter(item => !jobsToRemove.has(item.jobId))
          this.selected = [];
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    showHideSelected() {
      this.hiddenMode = !this.hiddenMode;
    },
    agentGroupsFormatted(agentGroups) {
      return agentGroups.join(", ");
    },
    updateItemsPerPage(newItemsPerPage) {
      this.itemsPerPage = newItemsPerPage;
      localStorage.setItem("items_per_page", newItemsPerPage); // Save for 7 days
    },
    saveColumnsState() {
      localStorage.setItem("columns", JSON.stringify(this.columnVisibility));
    },
    handleBeforeUnload(event) {
      this.items = [];
      StorageHelper.clearRawJobs();
    },
    requestAgentsGroup() {
      return this.$axios.get("/api/newman/availableAgentGroups")
          .then((response) => {
            return response.data;
          })
          .catch((error) => {
            console.error("Error fetching data:", error);
            return [];
          });
    }
  },
}
</script>

<style>
.jobsTable > .v-table__wrapper > table > thead > tr > th > .v-data-table-header__content {
  font-weight: bold;
  text-align: center;
  display: block;
}

.jobsTable > .v-table__wrapper > table > thead > tr > th,
.jobsTable > .v-table__wrapper > table > tbody > tr > td {
