<template>
  <v-card align="center">
    <v-expansion-panels style="width: 90%">
      <v-expansion-panel max-height="100px">
        <v-expansion-panel-title color="primary" height="60px" class="pt-2 d-block text-center">
          <div style="font-size: 1.3em; font-weight: bold;">Build: {{ buildDetails?.name }}</div>
          <div v-if="buildDetails" style="font-size: 1em; font-weight: bold;">( branch: {{ buildDetails?.branch }} )</div>
        </v-expansion-panel-title>
        <v-expansion-panel-text class="pa-0 ma-0">
          <v-container style="width: 100%; max-width: unset;">
            <div class="ml-3">
              <v-row class="my-0 mr-0" no-gutters v-for="(item, index) in info" :key="index">
                <v-col cols="2" align="start" class="font-weight-bold">
                  <div v-if="item.key == 'xap' || item.key == 'xappremium'" class="d-block text-right mr-16">
                    {{ item.title }}
                  </div>
                  <div v-else>
                    {{ item.title }}:
                  </div>
                </v-col>
                <v-col cols="10" align="start" v-if="buildDetails">
                  <div v-if="item.key=='id'" class="my-2"></div>
                  <div v-if="item.key=='resources'" class="my-2">
                    <div v-for="(path, key) in buildDetails[item.key]" :key="key" class="log-tab my-1 font-weight-bold">
                      <a :href="path" target="_blank">
                        &#x1F517; <span class="ml-2">{{ path }}</span>
                      </a>
                    </div>
                  </div>
                  <div v-else-if="item.key == 'xap' || item.key == 'xappremium'" class="log-tab">
                    <a :href="buildDetails[item.key]" target="_blank">
                    &#x1F517; <span class="ml-2">{{ buildDetails[item.key] }}</span>
                    </a>
                  </div>
                  <div v-else-if="item.key == 'commits'"></div>
                  <div v-else>
                    {{ (buildDetails && buildDetails[item.key]) || 'N/A' }}
                  </div>
                </v-col>
              </v-row>
            </div>
          </v-container>
        </v-expansion-panel-text>
      </v-expansion-panel>
    </v-expansion-panels>
    
    <JobsGrid no-jobs-count :build-id="id"/>
  </v-card>
</template>

<script>
import { parseBuildEntry } from '@/utils/utils';

export default {
  props: {
    id: String
  },
  data() {
    return {
      loading: true,
      buildDetails: null,
      info: [
        { key: "id", title: "Build Id" },
        { key: "branch", title: "Branch" },
        { key: "tags", title: "Tags" },
        { key: "buildTime", title: "Build Time" },
        { key: "resources", title: "Resources" },
        { key: "testsMetadata", title: "Test Metadata" },
        { key: "commits", title: "Commits" },
        { key: "xap", title: "xap:" },
        { key: "xappremium", title: "xap-premium:" }
      ],
    }
  },
  beforeMount() {
    this.initBuildDetails()
  },
  methods: {
    initBuildDetails() {
      this.$axios.get(`/api/newman/build/${this.id}`)
      .then((response) => {
        this.buildDetails = parseBuildEntry(response.data); // Update the component's data
        console.log(this.buildDetails)
        this.loading = false;
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
    }
  }
};
</script>