<template>
  <div name="grid">
    <v-card-title>
      <v-row justify="start" class="pa-0 ma-0">
        <v-col cols="3">
          <v-text-field
            v-model="search"
            density="compact"
            label="Search"
            prepend-inner-icon="mdi-magnify"
            variant="outlined"
            hide-details
            single-line
            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4">
          <div class="d-flex">
            <div class="d-block" style="position: relative;">
              <JobStatus :item="computedJobDetails" toggle />
              <v-overlay
                class="align-center justify-center"
                contained
                v-model="loading"
                opacity="0.2">
                <v-progress-circular indeterminate color="primary" size="24"/>
              </v-overlay>
            </div>
            <div class="d-block">
              <v-btn class="ml-2 font-weight-bold" 
                style="height: 100%" 
                text="Create suite" 
                color="primary"
                :disabled="loading"
                @click="$refs.createSuiteDialog.open(jobDetails?.suite)"/>
            </div>
          </div>
        </v-col>
        <slot name="header-tools"></slot>
      </v-row>
    </v-card-title>
  </div>

  <v-data-table
    :search="search"
    density="compact"
    :headers="headers"
    :items="filteredTests"
    class="tasksTable elevation-1"
    hover
    :loading="loading"
    :custom-filter="customFilter"
    :items-per-page="itemsPerPage"
    :items-per-page-options="[15, 20, 25, 50]"
  >
    <template v-slot:item.name="{ item }">
      <v-tooltip location="bottom" :open-delay="1000">
        <template v-slot:activator="{ props }">
          <router-link v-bind="props" class="font-bold" :to="{ name: 'TestDetails', params: { id: item.id } }">
            <span v-html="shortenTaskName(item)"></span>
          </router-link>
        </template>
        <span v-html="fullTaskName(item)"></span>
      </v-tooltip>
    </template>

    <template v-slot:item.status="{ item }">
      <div align="center">
        <v-chip
          variant="elevated"
          class="text-uppercase font-weight-bold text-body-2 text-mono"
          :color="getStatusColor(item.status)"
          :text="item.status"
          size="small"
          label
        ></v-chip>
      </div> 
    </template>

    <template v-slot:item.historyStats="{ item }">
      <div align="center" v-if="item.statsBranch">
        <div class="d-block">{{ coloredHistoryStats(item.statsBranch, false) }}</div>
      </div>  
      <div align="center" style="height: 1px" v-if="item.statsMaster != null">
        <div class="d-block">{{ coloredHistoryStats(item.statsMaster, true) }}</div>
      </div>
    </template>

    <template v-slot:item.history="{ item }">
      <div align="center">
        <v-btn value="history"
            rounded
            icon="mdi-eye"
            variant="outlined"
            color="primary"
            size="small"
            max-height="30px"
            max-width="30px"
            :to="{ name: 'History', params: { id: item.id } }"
        ></v-btn>
      </div>
    </template>
  </v-data-table>

  <DialogCreateSuite ref="createSuiteDialog" @createSuite:onConfirm="confirmCreateSuite"></DialogCreateSuite>
  
  <v-snackbar v-model="snackbar" timeout="5000">
    <div class="text-h6">Suite created: <strong>{{ createSuiteName }}</strong></div>
    <template v-slot:actions>
      <v-btn
        color="primary"
        variant="text"
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script setup>
import { getStatusColor, coloredHistoryStats } from '@/utils/utils';
</script>

<script>
import { inject } from 'vue';
import JobStatus from './JobStatus.vue';
import { parseJobTestEntry,parseJobEntry  } from '@/utils/utils';
import DialogCreateSuite from '../modals/DialogCreateSuite.vue';

export default {
  components: {
    JobStatus,
    DialogCreateSuite
  },
  props: {
    id: {type: String, required: true},
    status: {type: String},
    jobDetails: {type: Object}
  },
  beforeMount() {
    this.initTable();
    this.jobUpdate = inject("job-update");  // keep update on test status
    this.testUpdate = inject("test-update");
  },
  watch: {
    '$route.params': {
      immediate: true,
      handler(props) {
        this.testsFilter = {}
        if (props.status == 'FAILED3TIMES') {
          this.testsFilter.status = 'FAIL'
          this.testsFilter.runNumber = 3;
        } else {
          this.testsFilter.status = props.status
        }
      }
    },
    jobUpdate(job) {
      if (job && !this.loading) {
        const updatedJob = parseJobEntry(job);
        
        if (this.id && updatedJob.jobId != this.id) return; // do not update jobs not related to current buidlId if given
        
        this.liveStatus = updatedJob.status;
      }
    },
    testUpdate(test) {
      if (test && !this.loading) {
        const updatedTest = parseJobTestEntry(test);
        
        if (this.id && updatedTest.jobId != this.id) return; // do not update jobs not related to current buidlId if given
        
        const existingJobIndex = this.tests.findIndex(
          (test) => test.id === updatedTest.id
        );
        if (existingJobIndex !== -1) {
          this.tests[existingJobIndex] = updatedTest;  // Update only the specific item without affecting the rest
        } else {
          this.tests.unshift(updatedTest);
        }
      }
    }
  },
  data() {
    return {
      liveStatus: null,
      jobUpdate: null,
      testUpdate: null,
      search: "",
      selected: null,
      createSuiteName: null,
      itemsPerPage: 20,
      tests: [],
      testsFilter: {},
      headers: [
        { key: "name", title: "Name", width: "30%", minWidth: "500px", maxWidth: "500px"},
        { key: "status", title: "Status", width: "7%", minWidth: "120px", maxWidth: "120px"},
        { key: "historyStats", title: "History Stats", width: "7%", minWidth: "225px", maxWidth: "225px"},
        { key: "history", title: "History", width: "6%", minWidth: "55px", maxWidth: "55px"},
        { key: "errorMessage", title: "Error Message", width: "19%", minWidth: "160px", maxWidth: "160px"},
        { key: "assignedAgent", title: "Assigned Agent", width: "10%", minWidth: "180px", maxWidth: "180px"},
        { key: "agentGroup", title: "Agent Group", width: "10%", minWidth: "100px", maxWidth: "100px"},
        { key: "duration", title: "Duration", width: "5%", minWidth: "120px", maxWidth: "150px"},
      ],
      loading: true,
      dialogDisabled: true,
      snackbar: false
    }
  },
  computed: {
    computedJobDetails() {
      return { status: this.liveStatus || this.jobDetails?.status || [0,0,0,0,0], jobId: this.id};
    },
    filteredTests() {
      if (this.testsFilter == null || this.testsFilter.status == 'ALL') {
        return this.tests;
      } else {
        return this.tests.filter(t => {
          let result = t.status == this.testsFilter.status
          if (this.testsFilter.runNumber) {
            result = result && t.runNumber == this.testsFilter.runNumber
          }

          return result;
        });
      }
    }
  },
  methods: {
    customFilter(item, searchInput, metadata) {
      let result = false;

      const excludeFields = ["historyStats"];
      const entry = metadata.columns;
      for (const key in entry) {
        if (excludeFields.includes(key)) {
          continue;
        }

        const value = String(entry[key]);
        if (value.toLowerCase().includes(searchInput.toLowerCase())) {
          result = true;
          break;
        }
      }  
      return result;
    },
    initTable() {
      this.$axios.get(`/api/newman/job-tests-view?jobId=${this.id}&all=true&orderBy=name`)
        .then((response) => {
          this.tests = response.data.values.map(parseJobTestEntry)
          this.loading = false;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    },
    shortenTaskName(item) {
      let short = item.name
        .split('.')
        .map(section => section.replace(/^[a-z]+/, match => match[0]))
        .map(section => section.replace(/_[a-z]+/, match => '_' + match[1]))
        .join('.');

        return `<strong>${short}</strong> ${item.arguments.slice(1).join(" ")} [#${item.runNumber}]`;
    },
    fullTaskName(item) {
        return `<strong>${item.name}</strong> ${item.arguments.slice(1).join(" ")}`;
    },
    cancelCreateSuite() {
    },
    confirmCreateSuite(suiteName) {
      this.createSuiteName = suiteName;
      this.$axios.post("/api/newman/suite/failedTests", null, {
          params: {
            jobId: this.jobDetails.jobId,
            suiteName: this.createSuiteName
          }
      })
        .then((response) => {
          this.snackbar = true;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    }
  }
}
</script>

<style>
.tasksTable > .v-table__wrapper > table > thead > tr > th > .v-data-table-header__content{
  font-weight: bold;
  text-align: center;
  display: block;
}

.tasksTable > .v-table__wrapper > table > tbody > .v-data-table__tr {
  font-size: 1rem;
  font-weight: 500;
}

.tasksTable > .v-table__wrapper > table > tbody > tr > td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
} 
</style>
