<template>
    <v-container style="width: 100%" justify-center>
      <v-row no-gutters class="mt-2">
        <v-col cols="3" class="pr-6 d-flex">
          Suite:
        </v-col>
        <v-col cols="8" v-if="job">
          {{  localJob.suite }}
        </v-col>
      </v-row>
      <v-row no-gutters class="mt-5">
        <v-col cols="3" class="pr-6 d-flex">
          Build:
        </v-col>
        <v-col cols="8" v-if="job">
          {{  localJob.build }}
        </v-col>
      </v-row>
      <v-row no-gutters class="mt-5" align="center">
        <v-col cols="3" class="pr-6 d-flex">
          Priority:
        </v-col>
        <v-col cols="7">
          <v-select hide-details 
            color="blue" 
            variant="outlined" 
            density="compact" 
            v-model="localJob.priority" 
            :items="formattedPriorities"
            item-value="id"
            item-title="label"
          >
          </v-select>
        </v-col>
      </v-row>
      <v-row no-gutters class="mt-5">
        <v-col cols="3" class="pr-6 d-flex">
          Agent Groups:
        </v-col>
        <v-col cols="7">
          <v-select
            variant="outlined"
            density="compact"
            color="blue"
            v-model="localJob.agentGroups"
            :items="(localJob?.allAgentGroups)"
            chips
            multiple
            clearable
          >
            <template v-slot:chip="{ item }">
              <v-chip
                color="blue"
                closable
                size="large"
                @mousedown.stop
                @click:close="deselectAgent(item.title)"
              >
                {{ item.title }}
              </v-chip>
            </template>
          </v-select>
        </v-col>
      </v-row>
    </v-container>
</template>

<script>
import { defineComponent } from 'vue'

export default defineComponent({
    props: {
      text: { type: String },
      job: { type: Object, required: true, default: () => ({ref: null, build: null, suite: null, priority: null, agentGroups: null, allAgentGroups: null } )}
    },
    data() {
      return {
        priorities: [
          { id: 0, subtitle: "daily-default" },
          { id: 1, subtitle: "low" },
          { id: 2, subtitle: "high" },
          { id: 3, subtitle: "release-default" },
          { id: 4, subtitle: "urgent" },
        ],
        localJob: this.job
      }
    },
    computed: {
      formattedText() {
        return this.text.replace(/\n/g, '<br>');
      },
      formattedPriorities() {
        return this.priorities.map(priority => ({
          id: priority.id,
          label: `${priority.id} - ${priority.subtitle}`
        }));
      },
    },
    watch: {
      'localJob.agentGroups'(newVal, oldVal) {
        this.localJob.ref.agentGroups = this.localJob.agentGroups;  // update item visually inside the JobList
      }
    },
    methods: {
      deselectAgent(deselectItem) {
        this.localJob.agentGroups = this.localJob.agentGroups.filter(
          (agent) => agent !== deselectItem
        );
      }
    }
})
</script>
