<template>
  <v-card align="center">
    <div style="width: 80%">
      <v-card-title align="start" class="text-wrap">
        <div>
          <h3> Compare: </h3>
        </div>
      </v-card-title>

      <v-data-table
          hover
          hide-default-header
          hide-default-footer
          items-per-page="-1"
          density="compact"
          :items="items"
          class="elevation-1"
          :loading="loading"
      >
        <!-- Custom top header (merged) -->
        <template v-slot:top>
          <v-row no-gutters class="mb-2">
            <v-col cols="6" class="mt-2 mb-2 pl-2 pr-2">
              <router-link v-if="headers.left" class="font-bold" :to="headers.left?.id ? { name: 'BuildDetails', params: { id: headers.left.id} } : null">
                <h3 class="elevation-2 text-center col-header">
                  {{ headers.left.name }}
                </h3>
              </router-link>
            </v-col>
            <v-col cols="6" class="mt-2 mb-2 pr-2 pl-2">
              <router-link v-if="headers.right" class="font-bold" :to="headers.right?.id ? { name: 'BuildDetails', params: { id: headers.right.id || null} } : null">
                <h3 class="elevation-2 text-center col-header">
                  {{ headers.right.name }}
                </h3>
              </router-link>
            </v-col>
          </v-row>
        </template>

        <template v-slot:item="{ item }">
          <v-row no-gutters class="mb-2">
            <!-- Left-side items aligned right -->
            <v-col cols="3" class="d-flex justify-end pe-2">
              <span class="font-weight-bold font-medium">{{ item?.suiteLeft }}</span>
            </v-col>
            <v-col cols="3" class="d-flex justify-end pe-5">
              <div v-if="item.colLeft != null">
                <v-btn variant="elevated" value="TOTAL"
                       color="rgb(128,128,128)"
                       :text="item.colLeft[0].toString()"
                       :class="getButtonClass('total')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="SUCCESS"
                       color="rgba(92, 184, 92, 1)"
                       :text="item.colLeft[1].toString()"
                       :class="getButtonClass('success')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="FAIL"
                       color="rgba(217, 83, 79, 1)"
                       :text="item.colLeft[2].toString()"
                       :class="getButtonClass('fail')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="FAILED3TIMES"
                       color="rgba(139, 0, 0, 1)"
                       :text="item.colLeft[3].toString()"
                       :class="getButtonClass('fail3')"
                       size="small"
                ></v-btn>
              </div>
              <div v-else>
                <span>--</span>
              </div>
            </v-col>

            <!-- Right-side items aligned left -->
            <v-col cols="3" class="d-flex justify-start ps-2">
              <div v-if="item.colRight != null">
                <v-btn variant="elevated" value="FAILED3TIMES"
                       color="rgba(139, 0, 0, 1)"
                       :text="item.colRight[0].toString()"
                       :class="getButtonClass('fail3')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="FAIL"
                       color="rgba(217, 83, 79, 1)"
                       :text="item.colRight[1].toString()"
                       :class="getButtonClass('fail')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="SUCCESS"
                       color="rgba(92, 184, 92, 1)"
                       :text="item.colRight[2].toString()"
                       :class="getButtonClass('success')"
                       size="small"
                ></v-btn>
                <v-btn variant="elevated" value="TOTAL"
                       color="rgb(128,128,128)"
                       :text="item.colRight[3].toString()"
                       :class="getButtonClass('total')"
                       size="small"
                ></v-btn>
              </div>
              <div v-else>
                <span>--</span>
              </div>
            </v-col>
            <v-col cols="3" class="d-flex justify-start ps-5">
              <span class="font-weight-bold font-medium">{{ item.suiteRight }}</span>
            </v-col>
          </v-row>
        </template>
      </v-data-table>
    </div>
  </v-card>
</template>

<script>
  export default {
    data() {
      return {
        loading: true,
        search: null,
        test: null,
        headers: {},
        items: [],
      }
    },
    beforeMount() {
      this.initTable();
    },
    methods: {
      initTable() {
        const todaysBuildDate = new Date();
        todaysBuildDate.setDate(todaysBuildDate.getDate() - 1);

        const previousBuildDate = new Date(todaysBuildDate);
        previousBuildDate.setDate(previousBuildDate.getDate() - 1);

        const formatDate = (date) =>
            date.toISOString().split("T")[0]; // 'yyyy-MM-dd'

        const build1Date = formatDate(todaysBuildDate);
        const build2Date = formatDate(previousBuildDate);
        // const build1Date = '2025-04-08';
        // const build2Date = '2025-04-07';

        this.$axios
            .get(`/api/newman/build/${build1Date}/compare/${build2Date}`,{})
            .then((response) => {
              this.loading = false;
              const values = response.data

              const headerLeft = {
                id: values.buildLeftDetails && values.buildLeftDetails[0] || null,
                name: (values.buildLeftDetails && values.buildLeftDetails[1] || "No build found") + " (" + build1Date + ")"
              }
              const headerRight = {
                id: values.buildRightDetails && values.buildRightDetails[0] || null,
                name: (values.buildRightDetails && values.buildRightDetails[1] || "No build found") + " (" + build2Date + ")"
              }

              this.headers = {
                left: headerLeft,
                right: headerRight,
              };

              const keys = Object.keys(values.buildResults);
              this.items = keys.map(key => {
                const suiteData = values.buildResults[key];
                const left = suiteData.left;
                const right = suiteData.right;
                return {
                  suiteLeft: key,
                  colLeft: left && [left.totalTests, left.passedTests, left.failedTests, left.failed3TimesTests] || null,
                  suiteRight: key,
                  colRight: right && [right.failed3TimesTests, right.failedTests, right.passedTests, right.totalTests] || null
                };
              });
              // item.suiteLeft
              // item.colLeft

              // console.log(response.data)
              /*this.items = response.data.values.map(parseTestHistoryEntry); // Update the component's data
              this.loading = false;
              this.test = response.data.values[0].test.arguments.join(" ")
              console.log(this.entries)*/
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
              this.loading = false;
            });
      },
      getButtonClass(button) {
        const inactiveClassName = `inactive-button-${button}`
        return {
          'text-uppercase' : true,
          'chip-button': true,
          // 'active-button-font': isSelected,
          [inactiveClassName]: true, // Apply 'selected' class if button is active
        };
      },
    }
}
</script>

