  background-color: #f5f5f5;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.font-medium {
  font-size: 1.2em;
}

.chip-button {
  font-size: 1.2em;
  font-weight: bolder !important;
  height: 35px !important;
  width: 50px;
  min-width: 32px; /* Prevents button from expanding */
  padding: 0;      /* Removes default padding for a snug fit */
  margin-left: 1px;
  margin-right: 1px;
}

.inactive-button-success {
  border: 2px solid #5cb85c;
  color: #5cb85c;
}

.inactive-button-fail {
  border: 2px solid #d9534f;
  color: #d9534f;
}

.inactive-button-fail3 {
  border: 2px solid #8b0000;
  color: #8b0000;

}
.inactive-button-total {
  border: 2px solid grey;
  color: grey;
}
