<template>
  <v-card align="center">
    <v-expansion-panels class="d-flex" style="width: 80%" multiple>
      <v-row>
        <v-col cols="6">
          <v-expansion-panel max-height="100px">
            <v-expansion-panel-title color="primary" height="60px" class="pt-2 d-block text-center">
              <div style="font-size: 1.3em; font-weight: bold;">Job: {{ id }}</div>
              <div v-if="jobDetails" style="font-size: 1em; font-weight: bold;">( {{ jobDetails?.suite }} )</div>
            </v-expansion-panel-title>
            <v-expansion-panel-text class="pa-0 ma-0">
              <v-container style="width: 100%;">
                <div class="ml-6">
                  <v-row class="my-0 mr-0" no-gutters v-for="(item, index) in info.slice(0,12)" :key="index">
                    <v-col cols="3" align="end" class="font-weight-bold">{{ item.title }}:</v-col>
                    <v-col cols="78" align="start" class="ml-6" v-if="jobDetails">
                      <div v-if="item.key=='build'">
                        <router-link :to="{ name: 'BuildDetails', params: { id: jobDetails.buildId } }">
                          {{ jobDetails[item.key] }}
                        </router-link>
                      </div>
                      <div v-else-if="item.key=='state'">
                        <v-chip
                          variant="elevated"
                          class="text-uppercase font-weight-bold text-body-2 text-mono"
                          :color="getStatusColor(jobDetails[item.key])"
                          :text="jobDetails[item.key]"
                          size="small"
                          label
                        ></v-chip>
                      </div>
                      <div v-else>
                        {{ jobDetails[item.key] || 'N/A' }}
                      </div>
                    </v-col>
                  </v-row>
                </div>
              </v-container>
            </v-expansion-panel-text>
          </v-expansion-panel>
        </v-col>
        <v-col cols="6">
          <v-expansion-panel max-height="100px">
            <v-expansion-panel-title color="primary" height="60px" class="d-block text-center">
              <div style="font-size: 1.3em; font-weight: bold;">Job Agents</div>
            </v-expansion-panel-title>
            <v-expansion-panel-text>
              <v-container style="width: 100%;" align="left">
                  <v-list lines="one" v-if="jobDetails?.jobSetupLogs">
                    <v-list-item
                      density="none"
                      v-for="(entry, index) in Object.entries(jobDetails?.jobSetupLogs)" :key="index"
                      :title="entry[0]"
                      :href="entry[1]"
                      target="_blank" rel="noopener noreferrer"
                      link
                    ></v-list-item>
                  </v-list>
              </v-container>
            </v-expansion-panel-text>
          </v-expansion-panel>
        </v-col>
      </v-row>
    </v-expansion-panels>
    
    <TestsGrid :id="id" :status=status :jobDetails="jobDetails">
      <template v-slot:header-tools>
        <v-spacer></v-spacer>
        <v-col cols="2" align="right">
          <div class="d-flex align-center">
            <v-progress-linear v-if="jobDetails" 
              color="#5bc0de"
              height="25"
              width="25px"
              :model-value="jobDetails?.progress"
              :striped="jobDetails?.state == 'RUNNING'"
              rounded="lg"
            >
              <template v-slot:default="{ value }">
                <strong>{{ Math.ceil(value) }}%</strong>
              </template>
            </v-progress-linear>
            <span class="ml-3">{{ jobDetails?.state }}</span>
          </div>
        </v-col>
      </template>
    </TestsGrid>
  </v-card>
</template>

<script setup>
import { getStatusColor } from '@/utils/utils';
</script>

<script>
import { inject } from 'vue';
import { parseJobEntry, parseJobEntryExtra } from '@/utils/utils';
import TestsGrid from '../items/TestsGrid.vue';

export default {
  components: {
    TestsGrid
  },
  props: {
    id: String,
    status: String
  },
  beforeMount() {
    this.initJobDetails();
    this.jobUpdate = inject('job-update');
  },
  watch: {
    jobDetails(newVal) {
      document.title = this.pageTitle + ": *" + this.id.slice(this.id.length/1.5) + " (" + newVal.suite + ")";
    },
    jobUpdate(job) {
      if (job && !this.loading) {
        const updatedJob = parseJobEntry(job);
        
        if (this.id && updatedJob.jobId != this.id) return; // do not update jobs not related to current buidlId if given
        
        this.jobDetails = {...updatedJob, ...parseJobEntryExtra(job)}
      }
    },
  },
  data() {
    return {
      pageTitle: this.$route.meta.title,
      jobUpdate: null,
      jobDetails: null,
      info: [
        { key: "build", title: "Build" },
        { key: "state", title: "State" },
        { key: "suite", title: "Suite" },
        { key: "jdk", title: "Job Config" },
        { key: "submittedAt", title: "Submit Time" },
        { key: "startTime", title: "Start Time" },
        { key: "endTime", title: "End Time" },
        { key: "agentsNum", title: "# Agents" },
        { key: "preparingAgents", title: "# Prep. Agents" },
        { key: "agentGroups", title: "Agent Groups" },
        { key: "priority", title: "Priority" },
        { key: "submittedBy", title: "Submitted by" },
        { key: "status", title: "Status" },
        { key: "progress", title: "Progress" },
      ],
    }
  },
  methods: {
    initJobDetails() {
      this.$axios.get(`/api/newman/job/${this.id}`)
        .then((response) => {
          this.jobDetails = {...parseJobEntry(response.data), ...parseJobEntryExtra(response.data)}
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    }
  }
};
</script>

<style>

.v-expansion-panel > .v-expansion-panel-text > .v-expansion-panel-text__wrapper {
  padding: 0;
  padding-bottom: 8px;
}
</style>

