<template>
  <PromptDialog v-model="dialogVisible" @promptDialog:onConfirm="confirm">
    <template v-slot="modal">
      <v-container style="width: 85%" justify-center>
        <v-row no-gutters class="mt-5">
          <span>This will create a suite of all failing tests in this job</span>
        </v-row>
        
        <v-row no-gutters class="mt-5">
          <v-text-field
            v-model="visibleSuiteNameValue"
            label="Suite name"
            variant="outlined"
            hide-details
            @input="modal.onValidate(isSuiteNameValid())"
          >
            <template #prepend-inner>
              dev-
            </template>
          </v-text-field>
        </v-row>
      </v-container>
    </template>
  </PromptDialog>
</template>

<script>
import PromptDialog from './PromptDialog.vue';

export default {
  components: {
    PromptDialog
  },
  watch: {
    visibleSuiteNameValue(newValue) {
      this.modelSuiteNameValue = 'dev-' + newValue;
    }
  },
  data() {
    return {
      dialogVisible: false,
      visibleSuiteNameValue: '',
      modelSuiteNameValue: ''
    }
  },
  methods: {
    input() {
      console.log("emit data: " + this.modelSuiteNameValue)
    },
    isSuiteNameValid() {
      return this.modelSuiteNameValue.startsWith('dev-') && this.visibleSuiteNameValue.length > 2;
    },
    open(suite) {
      const currentDate = new Date();
      const formattedDate = currentDate.toISOString().split('T')[0].replace(/-/g, ''); // Format YYYYMMDD
      this.visibleSuiteNameValue = `failing-${suite}-${formattedDate}`;

      this.dialogVisible = true;
    },
    confirm() {
      this.$emit("createSuite:onConfirm", this.modelSuiteNameValue)
    }
  }
}
</script>
