<template>
  <v-card-title class="d-flex align-center pe-2">
      <GigasLogo/>
      <v-row align="stretch" class="my-0 mr-0" style="width: 100%">
        <v-col cols="2">
          <span class="ml-5">
            <v-icon :icon="pageIcon"></v-icon>  &nbsp;
            {{  pageTitle  }}
          </span>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="1" class="pa-0" align="center">
          <div class="font-small"> Total Agents:</div>
          <div> {{ totalAgents || 0 }} </div>
        </v-col>
        <v-col cols="1" class="pa-0" align="center">
          <div class="font-small"> Failing Agents:</div>
          <div> {{ failingAgents || 0 }} </div>
        </v-col>
        <v-col cols="1" class="pa-0" align="center">
          <div class="font-small"> Log size:</div>
          <div> {{ humanReadableLogSize || 0 }}</div>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="3">
          <v-row dense align="center" justify="end">
            <v-switch class="mr-4"
                v-model="isDarkTheme"
                :label="`${theme.global.name.value}`.toUpperCase()"
                @change="toggleTheme"
                hide-details
                density="compact"
            ></v-switch>
            <div> 
              {{ user }} &nbsp;
              <v-icon icon="mdi-account-box"></v-icon> 
            </div>
          </v-row>
        </v-col>
        <v-divider style="display: block;"></v-divider>
      </v-row>
  </v-card-title>

  <WebSocketProvider>
    <RouterView/>
  </WebSocketProvider>
  <!-- <JobsTable/> -->
  <SidePanel :user="user"/>
</template>

<script setup>
import SidePanel from './components/screens/SidePanel.vue';
import WebSocketProvider from './service/WebSocketProvider.vue';
import { useTheme } from 'vuetify';
import { onMounted } from 'vue';

const theme = useTheme()
let isDarkTheme;

function toggleTheme () {
  console.log("isDarkTheme: " + isDarkTheme)
  theme.global.name.value = theme.global.current.value.dark ? 'light' : 'dark'
  localStorage.setItem('theme', theme.global.name.value);
}

function initTheme() {
  const savedTheme = localStorage.getItem('theme');
  console.log("Theme: " + savedTheme);
  isDarkTheme = savedTheme === 'dark';
  if (savedTheme) {
    theme.global.name.value = savedTheme
  }
}

onMounted(() => {
  initTheme();
});

</script>

<script>
import {defineComponent} from 'vue';
import eventBus from "./service/eventBus";

export default defineComponent({
  components: {
    WebSocketProvider,
  },
  beforeMount() {
    this.fetchData();
    eventBus.$on("modified-agents-count", (count) => {this.totalAgents = count});
    eventBus.$on("modified-failing-agents", (count) => {this.failingAgents = count});
  },
  data() {
    return {
      user: null,
      logSize: 0,
      totalAgents: 0,
      failingAgents: 0,
      theme: null
    }
  },
  computed: {
    pageTitle() {
      return this.$route.meta.title || 'NO';
    },
    pageIcon() {
      return this.$route.meta.icon || 'NO';
    },
    humanReadableLogSize() {
      if (this.logSize == 0) return 0;

      let bytes = this.logSize
      if (bytes < 1000) return `${bytes} B`;
      let units = ['kB', 'MB', 'GB'];
      let index = -1;
      do {
        bytes /= 1000;
        index++;
      } while (bytes >= 1024 && index < units.length - 1);
      
      return `${bytes.toFixed(2)} ${units[index]}`;
    },
  },
  methods: {
    async fetchData() {
      try {
        await this.$axios.get("/api/newman/user")
            .then((response) => {
              this.user = response.data.userName;
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
            });

        await this.$axios.get("/api/newman/log/size")
            .then((response) => {
              this.logSize = response.data;
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
            });

        await this.$axios.get("/api/newman/agents/count")
            .then((response) => {
              this.totalAgents = response.data;
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
            });

        await this.$axios.get("/api/newman/agents/failing")
            .then((response) => {
              this.failingAgents = response.data;
            })
            .catch((error) => {
              console.error("Error fetching data:", error);
            });
      } catch (error) {
        console.error('Error fetching data:', error); // Handle errors
      }
    }
  }
});

</script>

