<template>
  <v-card-text v-if="text" class="mt-5">
    <div v-html="formattedText"></div>
  </v-card-text>
</template>

<script>
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    text: { type: String },
  },
  computed: {
    formattedText() {
      return this.text.replace(/\n/g, '<br>'); // Replace new line with <br>
    },
  }
})
</script>
