<template>
  <v-dialog v-model="showDialogValue" width="auto">
    <v-card
      :width="width"
      :min-width="minWidth"
      :max-width="maxWidth"
      elevation="16"
    >

      <v-card-title class="d-flex justify-center ml-n6 my-1">
          <v-icon v-if="prependIcon" :color="iconColor">{{ prependIcon }}</v-icon>
          <span v-html="title" class="ml-2"></span>
      </v-card-title>

      <v-divider></v-divider>

      <slot @validate="updateValidState"></slot>

      <v-card-actions class="pr-5 pb-5">
        <v-btn text="CONFIRM" :disabled="!showDialogValue" class="btn-confirm" width="120" elevation="5" variant="outlined" @click="confirm"></v-btn>
        <v-btn text="CANCEL" :disabled="!showDialogValue" width="80" variant="outlined" @click="cancel"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "PromptDialog",
  props: {
    modelValue: {type: Boolean},
    title: { type: String },
    prependIcon: { type: String, default: null },
    iconColor: { type: String },
    minWidth: { type: Number, default: 500 },
    maxWidth: { type: Number, default: 800 },
    width: { type: Number }
  },
  data() {
    return {
      isValidContent: true,
      showDialogValue: this.modelValue || false
    }
  },
  watch: {
    modelValue(val) {
      this.showDialogValue = val;
    }
  },
  methods: {
    confirm() {
      if (this.isValidContent) {
        this.$emit("promptDialog:onConfirm");
        this.closeDialog();
      }
    },
    cancel() {
      this.$emit("promptDialog:onCancel");
      this.closeDialog();
    },
    updateValidState(newValue) {
      this.isValidContent = newValue;
    },
    closeDialog() {
      this.showDialogValue = false;
      this.$emit("update:modelValue", false)
    },
    openDialog() {
      this.showDialogValue = true;
      this.$emit("update:modelValue", true)
    }
  },
};
</script>

<style>
.btn-confirm {
  color: white;
  font-weight: bold;
  border: none;
  background-color: rgb(33, 150, 243)
}
</style>

