<template>
  <v-card align="center">
    <div style="width: 80%">
      <v-card-title>
        <v-row dense justify="start" class="pa-0 ma-0">
          <v-col cols="3">
            <v-text-field
              v-model="search"
              density="compact"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              variant="outlined"
              hide-details
              single-line
              clearable
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-title>

      <v-data-table
        :search="search"
        density="compact"
        :headers="headers"
        :items="suites"
        class="suitesTable elevation-1"
        hover
        :loading="loading"
        :items-per-page="itemsPerPage"
        :items-per-page-options="[15, 20, 25, 50]"
      >
        <template v-slot:item.name="{ item }">
          <router-link class="font-bold" :to="{ name: 'SuiteDetails', params: { id: item.id } }">
            {{ item.name }}
          </router-link>
        </template>

        <!-- DELETE BUTTON -->
        <template v-slot:item.actions="{ item }">
          <v-btn
            tile
            icon="mdi-delete"
            height="26px"
            width="26px"
            rounded
            color="red"
            class="mr-1"
            @click="deleteSuitePrompt(item)"
            :disabled="!item.name.startsWith('dev-')"
          />
          <!-- COPY BUTTON -->
          <v-btn
            tile
            icon="mdi-content-copy"
            height="26px"
            width="26px"
            rounded
            color="orange"
            class="mr-1"
            style="color: white !important"
            @click="copySuitePrompt(item)"
          />
        </template>
      </v-data-table>
    </div>
  </v-card>

   <!-- Delete Suite -->
  <prompt-dialog
    ref="suiteDeleteDialog"
    prepend-icon="mdi-alert"
    icon-color="orange"
    title="Delete Suite"
    @promptDialog:onConfirm="confirmSuiteDelete"
    @promptDialog:onCancel="cancelPrompt"
    :disabled="dialogDisabled"
  >
    <dialog-prompt-body :text="deleteSelectedSuiteDialogBody" />
  </prompt-dialog>

  <!-- Copy Suite -->
  <dialog-copy-suite
    ref="suiteCopyDialog"
    prepend-icon="mdi-information"
    icon-color="blue"
    title="Copy Suite"
    @copySuite:onConfirm="confirmSuiteCopy"
    :disabled="dialogDisabled"
  >
  </dialog-copy-suite>

  <v-snackbar v-model="snackbar" timeout="5000" @click:close="onSnackbarClose">
    <div v-if="copiedSuiteName != null" class="text-h6">Suite copied: <strong>{{ copiedSuiteName }}</strong></div>
    <div v-else-if="deletedSuiteName != null" class="text-h6">Suite deleted: <strong>{{ deletedSuiteName }}</strong></div>
    <template v-slot:actions>
      <v-btn
        color="primary"
        variant="text"
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { parseSuiteEntry } from '@/utils/utils';
import PromptDialog from '../modals/PromptDialog.vue';
import DialogCopySuite from '../modals/DialogCopySuite.vue';
import DialogPromptBody from '../modals/DialogPromptBody.vue';

export default {
  components: {
    PromptDialog,
    DialogPromptBody,
    DialogCopySuite
  },
  beforeMount() {
    this.initTable();
  },
  data() {
    return {
      search: null,
      loading: true,
      selectedSuite: null,
      copiedSuiteName: null,
      deletedSuiteName: null,
      suites: [],
      headers: [
        { key: "name", title: "Name", width: "23%%"},
        { key: "id", title: "Id", width: "20%", maxWidth: "200px"},
        { key: "customVariables", title: "Config", width: "35%", maxWidth: "200px"},
        { key: "actions", title: "Actions", width: "5%"},
      ],
      itemsPerPage: 20,
      snackbar: false
    }
  },
  computed: {
    deleteSelectedSuiteDialogBody() {
      if (this.selectedSuite == null) return "";

      return `Would you like to delete the suite\n<strong>${this.selectedSuite.name} [${this.selectedSuite.id}]</strong> ?`;
    },
  },
  methods: {
    initTable() {
      this.$axios
        .get("/api/newman/suite", {params: {all: true}})
        .then((response) => {
          this.suites = response.data.values.map(parseSuiteEntry); // Update the component's data
          this.loading = false;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    },
    copySuitePrompt(item) {
      this.snackbar = false;
      this.dialogDisabled = false;
      this.selectedSuite = item;
      this.$refs.suiteCopyDialog.open(item.name);
    },
    deleteSuitePrompt(item) {
      this.snackbar = false;
      this.dialogDisabled = false;
      this.selectedSuite = item;
      this.$refs.suiteDeleteDialog.openDialog();
    },
    cancelPrompt() {
      this.dialogDisabled = true;
      this.selectedSuite = null;
    },
    confirmSuiteDelete() {
      this.deletedSuiteName = this.selectedSuite.name;
      this.$axios.delete(`/api/newman/suite/${this.selectedSuite.id}`)
        .then((response) => {
          this.snackbar = true;
          this.initTable();
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    confirmSuiteCopy(suiteName) {
      this.copiedSuiteName = suiteName;
      this.$axios.post(`/api/newman/suite/${this.selectedSuite.id}/${suiteName}`)
        .then((response) => {
          this.snackbar = true;
          this.initTable();
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    onSnackbarClose() {
      this.copiedSuiteName = null
      this.deletedSuiteName = null;
      this.selectedSuite = null;
    }
  }
}
</script>

<style>
.suitesTable > .v-table__wrapper > table > thead > tr > th > .v-data-table-header__content{
  font-weight: bold;
  text-align: center;
  display: block;
}

.suitesTable > .v-table__wrapper > table > tbody > .v-data-table__tr {
  font-size: 1rem;
  font-weight: 500;
}

.suitesTable > .v-table__wrapper > table > tbody > tr > td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
} 

.suitesTable tbody tr:nth-child(even) {
  background: hsla(230, 86%, 73%, 0.075);
}
</style>