import './assets/main.css'

import { createApp } from 'vue'

// Vuetify
import 'vuetify/styles'
import { createVuetify } from 'vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'
import router from './router'
import '@mdi/font/css/materialdesignicons.css' // Ensure you are using css-loader

import GigasLogo from './components/items/GigasLogo.vue'
import JobsGrid from './components/items/JobsGrid.vue'
import axios from "axios";

import App from './App.vue'

const vuetify = createVuetify({
    components,
    directives,
    theme: {
        defaultTheme: 'light'
    }
})

const app = createApp(App)
      .component('GigasLogo', GigasLogo)
      .component('JobsGrid', JobsGrid)
      .use(router)
      .use(vuetify)

async function loadConfig() {
  try {
    const response = await fetch('/config.json');
    window.config = await response.json();

    window.config.HOST = window.config?.WEB_HOST ?? window.location.hostname; // WebSocket will also use this
    if (import.meta.env.MODE === 'production') {  // don't remove this line otherwise localhost run will not be PROXIED
      axios.defaults.baseURL = `${window.config.WEB_PROTOCOL}://${window.config.HOST}:${window.config.WEB_PORT}`;
    }
    app.config.globalProperties.$axios = axios;
  } catch (error) {
    console.error('Error loading config:', error);
  }
}

loadConfig().then(() => {app.mount('#app')});