const ROW_JOBS_KEY = 'raw_jobs';

const StorageHelper = {
    hasRawJobsSaved() {
      const storedItem = sessionStorage.getItem(ROW_JOBS_KEY);
      return storedItem !== null;  // Returns true if the item exists, false otherwise
    },

    // Save items to sessionStorage
    saveRawJobs(items) {
      // Convert items to string before saving
      sessionStorage.setItem(ROW_JOBS_KEY, JSON.stringify(items));
    },
  
    // Get items from sessionStorage
    getRawJobs() {
      const storedItems = sessionStorage.getItem(ROW_JOBS_KEY);
      return storedItems ? JSON.parse(storedItems) : [];
    },
  
    // Clear all sessionStorage items
    clearRawJobs() {
      sessionStorage.removeItem(ROW_JOBS_KEY);
    },
  
    // Method to keep track and update items
    updateRawJob(newItem) {
      const currentItems = this.getRawJobs();

      if (currentItems.length == 0) return;
    
      const existingItemIndex = currentItems.findIndex(item => item.id === newItem.id);
      
      if (existingItemIndex !== -1) {
        currentItems[existingItemIndex] = { ...currentItems[existingItemIndex], ...newItem };
      } else {
        currentItems.unshift(newItem);
      }

      this.saveRawJobs(currentItems);
    },
  };
  
  export default StorageHelper;