<template>
  <v-card flat align="center">
    <v-card-title align="start" style="width: 90%;" class="text-wrap">
      <h3> Suite </h3>
    </v-card-title>

    <v-container style="width: 90%; max-width: unset;">
      <div class="ml-10" align="left">
        <v-row align="start" class="my-0 mr-0" no-gutters v-for="(item, index) in info" :key="index">
          <v-col cols="1" align="start" class="font-weight-bold pt-2">{{ item.title }}:</v-col>
          <v-col cols="11" v-if="suiteDetails">
            <div class="mb-2" align="start">
              <div v-if="item.key=='name' || item.key=='customVariables' || item.key=='requirements'">
                <v-text-field
                  color="primary"
                  v-model="suiteDetails[item.key]"
                  variant="outlined" 
                  hide-details 
                  density="compact"
                  width="75%">
                </v-text-field>
              </div>
              <div v-else-if="item.key=='criteria'">
                <textarea
                  class="custom-textarea"
                  v-model="suiteDetails[item.key]"
                  :rows=20
                ></textarea>
              </div>
              <div v-else>
                {{ suiteDetails[item.key] }}
              </div>
            </div>
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col cols="1"/>
          <v-col cols="11">
            <v-btn color="primary" :disabled="loading" class="font-bold" @click="$refs.suiteApplyChangeDialog.openDialog()">Save</v-btn>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </v-card>

  <!-- Apply suite update -->
  <prompt-dialog
    ref="suiteApplyChangeDialog"
    prepend-icon="mdi-information-box"
    icon-color="primary"
    title="Suite Update"
    @promptDialog:onConfirm="confirmApplySuite"
  >
    <dialog-prompt-body :text="suiteApplyDialogBody" />
  </prompt-dialog>

  <v-snackbar v-model="snackbar" timeout="5000">
    <div class="text-h6">Suite updated: <strong>{{ suiteDetails.name }}</strong></div>
    <template v-slot:actions>
      <v-btn
        color="primary"
        variant="text"
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { parseSuiteEntry } from '@/utils/utils';
import PromptDialog from '../modals/PromptDialog.vue';
import DialogPromptBody from '../modals/DialogPromptBody.vue';

export default {
  components: {
    PromptDialog,
    DialogPromptBody,
  },
  props: {
    id: String
  },
  beforeMount() {
    this.initSuiteDetails();
  },
  computed: {
    suiteApplyDialogBody() {
      return `Would you like to apply changes to the suite \n<strong>${this.suiteDetails?.name}</strong> ?`;
    }
  },
  data() {
    return {
      jsonCriteria: "JsonGoesHere: 123}",
      updateSuite: null,
      suiteDetails: null,
      loading: true,
      snackbar: false,
      info: [
        { key: "name", title: "Name" },
        { key: "id", title: "Id" },
        { key: "customVariables", title: "Variables" },
        { key: "requirements", title: "Requirements" },
        { key: "criteria", title: "Criteria" },
      ]
    }
  },
  
  methods: {
    initSuiteDetails() {
      this.$axios.get(`/api/newman/suite/${this.id}`)
        .then((response) => {
          this.suiteDetails = parseSuiteEntry(response.data)
          this.loading = false;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.loading = false;
        });
    },
    confirmApplySuite() {
      let body = {
        ...this.suiteDetails,
        criteria: JSON.parse(this.suiteDetails.criteria)
      }
      this.$axios
        .post("/api/newman/update-suite", body)
        .then((response) => {
          this.snackbar = true;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    }
  }
};
</script>

<style>
.custom-textarea {
  width: 75%; /* Full-width by default */
  padding: 8px; /* Compact padding */
  font-size: 16px; /* Adjust font size for compact density */
  font-weight: 500;
  line-height: 1.3; /* Ensure good readability */
  border: 1px solid #ccc; /* Outlined border */
  border-radius: 4px; /* Rounded corners */
  resize: both; /* Allow resizing horizontally and vertically */
  box-shadow: none; /* Remove default shadow */
  outline: none; /* Remove blue outline on focus */
  font-family: Monaco, Consolas, monospace;
  overflow: auto; /* Handle overflow gracefully */
}

.custom-textarea:focus {
  border: 2px solid #1976d2; /* Highlight border on focus (Vuetify blue) */
  padding: 7px;
  
}

</style>